               @{class-name} { @rules(); .template(@val); }\n            }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index(@class-prefix, @rules, @index + 1, @values);\n    }\n\n    #auto-generate-no-convert(@class-prefix, @rules, @values) {\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, 1, @values);\n    }\n    #auto-generate-with-index-no-convert(@class-prefix, @rules, @index, @values) when (@index <= length(@values)) {\n        // Extract our values\n        @val: extract(@values, @index);\n\n        //  When our values are numeric...\n        & when (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, @index + 1, @values);\n    }\n\n\n    //  ========================================================================\n    //  $   CREATE FONT FACE\n    //  ------------------------------------------------------------------------\n    #font-face(@type, @name, @style: normal, @local: false, @ascent: normal) {\n        #d-internal-config();\n\n        if((@generate-font-face = true), each(@type, {\n          @font-face {\n            font-style: @style;\n            font-weight: @key;\n            font-family: @name;\n            src: if((@local = true ), local(@value), url(\"@{define-font-path}@{value}.woff2\") format(\"woff2\"));\n            ascent-override: @ascent;\n          };\n        }));\n    }\n\n    #gradient-classes(@color) {\n      //  COLOR STOP 1, STARTING STOP\n      .d-bgg-from-@{color},\n      .h\\:d-bgg-from-@{color}:hover,\n      .f\\:d-bgg-from-@{color}:focus {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .f\\:d-bgg-from-@{color}:focus-within {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .fv\\:d-bgg-from-@{color}:focus-visible {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-from-@{color}', {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      });\n\n      //  COLOR STOP 2, ENDING STOP\n      .d-bgg-to-@{color},\n      .h\\:d-bgg-to-@{color}:hover,\n      .f\\:d-bgg-to-@{color}:focus {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      .f\\:d-bgg-to-@{color}:focus-within {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      .fv\\:d-bgg-to-@{color}:focus-visible {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-to-@{color}', {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      });\n    }\n\n    #gradient-stops(@color) {\n      #d-internals #gradient-classes(~\"@{color}-600\");\n      #d-internals #gradient-classes(~\"@{color}-500\");\n      #d-internals #gradient-classes(~\"@{color}-400\");\n      #d-internals #gradient-classes(~\"@{color}-300\");\n      #d-internals #gradient-classes(~\"@{color}-200\");\n      #d-internals #gradient-classes(~\"@{color}-100\");\n    }\n\n    #build-flex-columns(@num, @count: 1) when (@count =< @num) {\n    .d-fl-col@{count} {\n      display: flex;\n\n      //  Automatically make all direct child to obey the parent class\n      > * {\n        --fl-gap: 0;\n        --fl-basis: calc(100% / @count);\n\n        display: inline-flex;\n        margin: var(--fl-gap);\n        flex: 1 calc(var(--fl-basis) - ((var(--fl-gap) * 2)));\n\n        //  Add in a gap to items after the first row\n        &:nth-child(-n + @{count}) {\n          margin-top: 0;\n        }\n      }\n\n      //  If flex column is in a column direction (and became rows),\n      //  change the margin direction.\n      &.d-fd-column > * {\n        margin: var(--fl-gap) 0;\n      }\n    }\n\n    //  Iterate our loop until we hit our grid column total\n    #d-internals #build-flex-columns(@num, @count + 1);\n  }\n\n    //  ========================================================================\n    //  $   SPACING CLASSES\n    //  ------------------------------------------------------------------------\n    #spacing-classes(@BU, @prefix, @suffix: '') {\n      @stop-100: ~\"@{prefix}-100@{suffix}\";\n      @stop-200: ~\"@{prefix}-200@{suffix}\";\n      @stop-300: ~\"@{prefix}-300@{suffix}\";\n      @stop-400: ~\"@{prefix}-400@{suffix}\";\n      @stop-500: ~\"@{prefix}-500@{suffix}\";\n      @stop-600: ~\"@{prefix}-600@{suffix}\";\n      @stop-700: ~\"@{prefix}-700@{suffix}\";\n      @stop-800: ~\"@{prefix}-800@{suffix}\";\n\n      body {\n        --@{stop-100}:       calc(@BU / pow(2, 3));\n        --@{stop-200}:       calc(@BU / pow(2, 2));\n        --@{stop-300}:       calc(@BU / pow(2, 1));\n        --@{stop-400}:       @BU;\n        --@{stop-500}:       calc(@BU * pow(2, 1));\n        --@{stop-600}:       calc(@BU * pow(2, 2));\n        --@{stop-700}:       calc(@BU * pow(2, 3));\n        --@{stop-800}:       calc(@BU * pow(2, 4));\n      }\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: Avatars\n//\n//  These are avatars classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/avatars\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//    • SIZES\n//    • COLORS\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-avatar {\n  //  Component CSS Vars\n  //  ------------------------------------------------------------------------\n  --avatar-color-background: var(--gold-100);\n  --avatar-color-text: var(--fc-secondary);\n  --avatar-gradient-angle: 180deg;\n  --avatar-gradient-stop-1: var(--gold-100);\n  --avatar-gradient-stop-2: var(--magenta-100);\n  --avatar-gradient-stop-3: var(--blue-200);\n  --avatar-gradient:\n    conic-gradient(\n      from var(--avatar-gradient-angle) at 50% 50%,\n      var(--avatar-gradient-stop-1) 0deg,\n      var(--avatar-gradient-stop-2) 180deg,\n      var(--avatar-gradient-stop-3) 360deg\n    );\n  --avatar-size-shape: var(--size-600);\n  --avatar-size-text: var(--fs-200);\n  --avatar-presence-position-right: var(--sun2);\n  --avatar-presence-position-bottom: var(--sun2);\n\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: var(--avatar-size-shape);\n  height: var(--avatar-size-shape);\n  color: var(--avatar-color-text);\n  font-weight: var(--fw-bold);\n  font-size: var(--avatar-size-text);\n  line-height: var(--lh-100);\n  background-color: var(--avatar-color-background);\n  background-image: var(--avatar-gradient);\n  border-radius: var(--br-pill);\n\n  &__image {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    border-radius: inherit;\n  }\n\n  &__presence {\n    position: absolute;\n    right: var(--avatar-presence-position-right);\n    bottom: var(--avatar-presence-position-bottom);\n    display: flex;\n  }\n\n  //  --  SIZES\n  //  ------------------------------------------------------------------------\n\n  &--xs {\n    --avatar-size-shape: calc(var(--size-500) + var(--size-200));\n    --avatar-presence-position-right: var(--sun4);\n    --avatar-presence-position-bottom: var(--sun4);\n  }\n\n  &--sm {\n    --avatar-size-shape: calc(var(--size-500) + var(--size-400));\n    --avatar-size-text: var(--fs-100);\n    --avatar-presence-position-right: var(--sun2);\n    --avatar-presence-position-bottom: var(--sun2);\n  }\n\n  &--md {\n    --avatar-size-shape: var(--size-600);\n    --avatar-size-text: var(--fs-200);\n    --avatar-presence-position-right: var(--sun1);\n    --avatar-presence-position-bottom: var(--sun1);\n  }\n\n  &--lg {\n    --avatar-size-shape: calc(var(--size-600) + var(--size-500));\n    --avatar-size-text: var(--fs-300);\n    --avatar-presence-position-right: var(--su1);\n    --avatar-presence-position-bottom: var(--su1);\n  }\n\n  &--xl {\n    --avatar-size-shape: var(--size-700);\n    --avatar-size-text: var(--fs-400);\n    --avatar-presence-position-right: var(--su4);\n    --avatar-presence-position-bottom: var(--su4);\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: BADGES\n//\n//  These are badge classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/badges\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//    • COLORS\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-badge {\n  //  Component CSS Vars\n  --badge--fc: var(--fc-primary);\n  --badge--bgc: var(--black-200);\n\n  display: inline-block;\n  min-width: calc(var(--su6) + var(--su1));\n  padding: calc(var(--su6) / 4) var(--su6);\n  color: var(--badge--fc);\n  font-weight: bold;\n  font-size: var(--fs-100);\n  line-height: var(--lh6);\n  text-align: center;\n  text-transform: uppercase;\n  background-color: var(--badge--bgc);\n  border-radius: var(--su4);\n}\n\n\n//  ============================================================================\n//  $   STYLE VARIATIONS\n//  ----------------------------------------------------------------------------\n.d-badge--white {\n  --badge--bgc: var(--white);\n}\n\n.d-badge--black-700 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--black-700);\n}\n\n.d-badge--purple-100 {\n  --badge--bgc: var(--purple-100);\n}\n\n.d-badge--purple-300 {\n  --badge--bgc: var(--purple-300);\n}\n\n.d-badge--purple-400 {\n  --badge--bgc: var(--purple-400);\n  --badge--fc: var(--white);\n}\n\n.d-badge--purple-500 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--purple-500);\n}\n\n.d-badge--orange-400 {\n  --badge--bgc: var(--orange-400);\n}\n\n.d-badge--magenta-100 {\n  --badge--bgc: var(--magenta-100);\n}\n\n.d-badge--magenta-300 {\n  --badge--bgc: var(--magenta-300);\n}\n\n.d-badge--magenta-400 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--magenta-400);\n}\n\n.d-badge--magenta-500 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--magenta-500);\n}\n\n.d-badge--gold-200 {\n  --badge--bgc: var(--gold-200);\n}\n\n.d-badge--green-400 {\n  --badge--bgc: var(--green-400);\n}\n\n.d-badge--red-300 {\n  --badge--bgc: var(--red-300);\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BANNERS\n//\n//  These are the styles for banners, which are a type of notice used\n//  to alert users about important information relevant to them.\n//  For further documentation of these styles, please visit:\n//  https://dialpad.design/components/banners\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n.d-banner {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --banner--bgc: var(--black-100);\n    --banner--fc: var(--fc-primary);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    left: 0;\n    z-index: var(--zi-navigation-fixed);\n    display: flex;\n    box-sizing: border-box;\n    width: 100%;\n    min-height: var(--su48);\n    color: var(--banner--fc);\n    font-size: var(--fs-200);\n    line-height: var(--lh6);\n    background-color: var(--banner--bgc);\n    border-radius: 0;\n    box-shadow: none;\n\n    // If you want to hide and reveal the banner\n    &[aria-hidden='true'] {\n        --topbar-height: var(--sun64);\n\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n\n    //  If you want to put the banner above the topbar\n    &.d-banner--pinned {\n        z-index: calc(~'var(--zi-navigation-fixed) + 1');\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n.d-banner--transform {\n    transform: translate3d(0, var(--topbar-height), 0);\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-banner__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n    width: 100%;\n    max-width: 128rem;\n    min-height: 100%;\n    margin: 0 auto;\n    padding: var(--su8);\n\n    .d-notice__content {\n        flex-direction: row;\n        align-items: center;\n\n        .d-flow4();\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-banner.d-banner--important {\n    --banner--bgc: var(--black-800);\n    --banner--fc: var(--black-100);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-banner--error {\n    --banner--bgc: var(--red-100);\n    --banner--fc: var(--red-500);\n\n    &.d-banner--important {\n        --banner--bgc: var(--red-300);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-banner--info {\n    --banner--bgc: var(--purple-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--purple-400);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-banner--success {\n    --banner--bgc: var(--green-100);\n    --banner--fc: var(--green-500);\n\n    &.d-banner--important {\n        --banner--bgc: var(--green-200);\n        --banner--fc: var(--green-500);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-banner--warning {\n    --banner--bgc: var(--gold-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--gold-200);\n        --banner--fc: var(--fc-primary);\n    }\n}\n","//@import (reference) '../variables/spacing.less';\n//@import (reference) 'internals.less';\n\n//\n//  DIALTONE\n//  UTILITIES: SPACING\n//\n//  These are spacing utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/spacing\n//\n//  TABLE OF CONTENTS\n//  • AUTO-SPACING\n//    - VERTICALLY\n//    - HORIZONTALLY\n//  • MARGIN\n//    - POSITIVE MARGINS\n//      •• ALL DIRECTIONS\n//      •• TOP MARGIN\n//      •• RIGHT MARGIN\n//      •• BOTTOM MARGIN\n//      •• LEFT MARGIN\n//      •• X-AXIS MARGIN\n//      •• Y-AXIS MARGIN\n//    - NEGATIVE MARGINS\n//      •• ALL DIRECTIONS\n//      •• TOP MARGIN\n//      •• RIGHT MARGIN\n//      •• BOTTOM MARGIN\n//      •• LEFT MARGIN\n//      •• X-AXIS MARGIN\n//      •• Y-AXIS MARGIN\n//  • PADDING\n//    - ALL DIRECTIONS\n//    - TOP PADDING\n//    - RIGHT PADDING\n//    - BOTTOM PADDING\n//    - LEFT PADDING\n//    - X-AXIS PADDING\n//    - Y-AXIS PADDING\n//    - RESET PADDING\n//\n//  ============================================================================\n//  $   AUTO-SPACING\n//  ============================================================================\n//  $$  THE STACK\n//      Instead of stlying specific elements when we're unaware of their context,\n//      this applies margin to context. The margin is only applied when an\n//      element is proceeded by another element.\n//  ----------------------------------------------------------------------------\n.d-stack0 { > * + * { margin-top: @su0 !important; } }\n.d-stack1 { > * + * { margin-top: @su1 !important; } }\n.d-stack2 { > * + * { margin-top: @su2 !important; } }\n.d-stack4 { > * + * { margin-top: @su4 !important; } }\n.d-stack6 { > * + * { margin-top: @su6 !important; } }\n.d-stack8 { > * + * { margin-top: @su8 !important; } }\n.d-stack12 { > * + * { margin-top: @su12 !important; } }\n.d-stack16 { > * + * { margin-top: @su16 !important; } }\n.d-stack24 { > * + * { margin-top: @su24 !important; } }\n.d-stack32 { > * + * { margin-top: @su32 !important; } }\n.d-stack48 { > * + * { margin-top: @su48 !important; } }\n.d-stack64 { > * + * { margin-top: @su64 !important; } }\n.d-stack72 { > * + * { margin-top: @su72 !important; } }\n.d-stack84 { > * + * { margin-top: @su84 !important; } }\n.d-stack96 { > * + * { margin-top: @su96 !important; } }\n.d-stack102 { > * + * { margin-top: @su102 !important; } }\n.d-stack114 { > * + * { margin-top: @su114 !important; } }\n.d-stack128 { > * + * { margin-top: @su128 !important; } }\n\n.d-flow0 { > * + * { margin-left: @su0 !important; } }\n.d-flow1 { > * + * { margin-left: @su1 !important; } }\n.d-flow2 { > * + * { margin-left: @su2 !important; } }\n.d-flow4 { > * + * { margin-left: @su4 !important; } }\n.d-flow6 { > * + * { margin-left: @su6 !important; } }\n.d-flow8 { > * + * { margin-left: @su8 !important; } }\n.d-flow12 { > * + * { margin-left: @su12 !important; } }\n.d-flow16 { > * + * { margin-left: @su16 !important; } }\n.d-flow24 { > * + * { margin-left: @su24 !important; } }\n.d-flow32 { > * + * { margin-left: @su32 !important; } }\n.d-flow48 { > * + * { margin-left: @su48 !important; } }\n.d-flow64 { > * + * { margin-left: @su64 !important; } }\n.d-flow72 { > * + * { margin-left: @su72 !important; } }\n.d-flow84 { > * + * { margin-left: @su84 !important; } }\n.d-flow96 { > * + * { margin-left: @su96 !important; } }\n.d-flow102 { > * + * { margin-left: @su102 !important; } }\n.d-flow114 { > * + * { margin-left: @su114 !important; } }\n.d-flow128 { > * + * { margin-left: @su128 !important; } }\n\n\n//  ============================================================================\n//     MARGIN\n//  ============================================================================\n//  ============================================================================\n//  $  POSITIVE MARGINS\n//  ============================================================================\n//  $$ ALL DIRECTIONS\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-m',\n    { .template(@value) { margin: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ TOP MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mt',\n    { .template(@value) { margin-top: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ RIGHT MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mr',\n    { .template(@value) { margin-right: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ BOTTOM MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mb',\n    { .template(@value) { margin-bottom: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ LEFT MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-ml',\n    { .template(@value) { margin-left: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ X-AXIS (LEFT & RIGHT)\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mx',\n    { .template(@value) { margin-right: @value !important; margin-left: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ Y-AXIS (TOP & BOTTOM)\n//  -------------