(\n    '.d-py',\n    { .template(@value) { padding-top: @value !important; padding-bottom: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 unset\n);\n","//\n//  DIALTONE\n//  COMPONENTS: BREADCRUMBS\n//\n//  These are breadcrumb classes for Dialpad's design system Dialtone.\n//  For further information, visit https://dialpad.design/components/breadcrumbs\n//\n//  TABLE OF CONTENTS\n//  • WRAPPER\n//  • ITEMS & LINKS\n//  • INVERTED STYLE\n//\n//  ============================================================================\n//  $  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs > ol {\n    display: flex;\n    flex-wrap: wrap;\n    align-items: flex-start;\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n    font-size: var(--fs-200);\n    line-height: var(--lh6);\n    list-style: none;\n}\n\n//  ============================================================================\n//  $  ITEMS & LINKS\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs {\n    //  --  INDIVIDUAL ITEMS\n    .d-breadcrumbs__item {\n        position: relative;\n        margin: 0;\n        margin-left: var(--su6);\n        padding: 0;\n        list-style: none;\n\n        //  Provide a forward slash with each element except when it's the last one.\n        &:not(:last-of-type) {\n            margin-right: var(--su12);\n\n            &::before {\n                position: absolute;\n                right: var(--sun12);\n                margin-top: var(--su1);\n                color: var(--muted-color);\n                content: '/';\n            }\n        }\n    }\n\n    //  --  SELECTED STATE (CURRENT PAGE)\n    .d-breadcrumbs__item--selected .d-link {\n        text-decoration: none;\n        cursor: default;\n    }\n}\n\n\n//  ============================================================================\n//  $  INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs.d-breadcrumbs--inverted {\n    //  --  ITEM\n    .d-breadcrumbs__item:not(:last-of-type)::before {\n        color: var(--inverted-color);\n    }\n\n    //  --  SELECTED STATE (CURRENT PAGE)\n    .d-breadcrumbs__item--selected .d-link {\n        &:hover {\n            color: var(--inverted-color);\n        }\n    }\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BUTTONS\n//\n//  These are button classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/buttons\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • VISUAL STYLES\n//  • ALTERNATE STYLES\n//  • SIZES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-btn {\n    //  Component specific CSS Vars\n    //  ------------------------------------------------------------------------\n    --button--fc: var(--primary-color);\n    --button--bgc: transparent;\n    --button--bc: transparent;\n    --button--br: var(--base--corner-radius);\n\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    padding: calc(var(--su8) - var(--su1));  // Subtract border\n    color: var(--button--fc);\n    font-size: var(--fs-200);\n    font-family: inherit;\n    line-height: var(--lh4);\n    text-transform: inherit;\n    text-decoration: none;\n    vertical-align: middle;\n    background-color: var(--button--bgc);\n    border: 0.1rem solid var(--button--bc);\n    border-radius: var(--button--br);\n    box-shadow: var(--button--bs);\n    cursor: pointer;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: 75ms;\n    transition-property: background-color, border, box-shadow;\n    user-select: none;\n    fill: currentColor;\n\n    //  --  STATES\n    //  ------------------------------------------------------------------------\n    &:hover {\n        --button--fc: var(--primary-color-hover);\n        --button--bgc: hsla(var(--primary-color-hsl) ~' / ' 3%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--primary-color-hover);\n        --button--bgc: hsla(var(--primary-color-hsl) ~' / ' 9%);\n    }\n\n\n    &:focus-visible {\n      outline: none;\n      box-shadow: var(--bs-focus-ring);\n    }\n\n    &[disabled] {\n        color: var(--fc-disabled);\n        background-color: var(--black-300);\n        border-color: transparent;\n        cursor: not-allowed;\n        transition: none;\n    }\n\n    .d-btn__icon .d-svg {\n        width: @icon16;\n        height: @icon16;\n    }\n}\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n//  $$  EXTRA SMALL\n//  ----------------------------------------------------------------------------\n.d-btn--xs {\n    padding: calc(var(--su6) - var(--su1)) calc(var(--su8) - var(--su1));  // Subtract border\n    font-size: var(--fs-100);\n\n    .d-btn__icon .d-svg {\n        width: @icon12;\n        height: @icon12;\n    }\n}\n\n//  $$ SMALL\n//  ----------------------------------------------------------------------------\n.d-btn--sm {\n    padding: calc(var(--su6) - var(--su1)) calc(var(--su8) - var(--su1));  // Subtract border\n    font-size: var(--fs-200);\n    line-height: var(--lh6);\n\n    .d-btn__icon .d-svg {\n        width: @icon14;\n        height: @icon14;\n    }\n}\n\n//  $$  LARGE\n//  ----------------------------------------------------------------------------\n.d-btn--lg {\n    padding: calc(var(--su12) - var(--su1)) calc(var(--su16) - var(--su1)); // Subtract border\n    font-size: var(--fs-300);\n\n    .d-btn__icon .d-svg {\n        width: @icon20;\n        height: @icon20;\n    }\n}\n\n//  $$  EXTRA LARGE\n//  ----------------------------------------------------------------------------\n.d-btn--xl {\n    padding: calc(var(--su12) - var(--su1)) calc(var(--su16) - var(--su1)); // Subtract border\n    font-size: var(--fs-300);\n    line-height: var(--lh8);\n\n    .d-btn__icon .d-svg {\n        width: @icon24;\n        height: @icon24;\n    }\n}\n\n//  ============================================================================\n//  $   BUTTON LABEL\n//  ----------------------------------------------------------------------------\n.d-btn__label {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n}\n\n//  ============================================================================\n//  $   BUTTON WITH ICON\n//  ----------------------------------------------------------------------------\n.d-btn--vertical {\n    flex-direction: column;\n}\n\n.d-btn__icon {\n    display: flex;\n    line-height: 0;\n}\n\n.d-btn__icon--left,\n.d-btn__icon--right {\n    margin-top: var(--sun4);\n    margin-bottom: var(--sun4);\n}\n\n.d-btn__icon--top,\n.d-btn__icon--bottom {\n    margin-right: var(--sun4);\n    margin-left: var(--sun4);\n}\n\n.d-btn__icon--left,\n.d-btn__icon--top {\n    order: -1;\n}\n\n.d-btn__icon--right,\n.d-btn__icon--bottom {\n    order: 1;\n}\n\n.d-btn__icon--left:not(:only-child) {\n    margin-left: var(--sun2);\n}\n\n.d-btn__icon--right:not(:only-child) {\n    margin-right: var(--sun2);\n}\n\n.d-btn__icon--left ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--right {\n    margin-left: var(--su4);\n}\n\n.d-btn__icon--right ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--left {\n    margin-right: var(--su4);\n}\n\n.d-btn__icon--top ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--bottom {\n    margin-top: var(--su4);\n}\n\n.d-btn__icon--bottom ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--top {\n    margin-bottom: var(--su4);\n}\n\n//  ============================================================================\n//  $   VISUAL STYLES\n//  ----------------------------------------------------------------------------\n//  $$  CIRCLE BUTTONS\n//  ----------------------------------------------------------------------------\n.d-btn--circle {\n    --button--fc: var(--black-800);\n    --button--br: var(--br-circle);\n\n    padding: calc(var(--su8) + var(--su1));\n    transition-duration: 150ms;\n\n    &:hover {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 3%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 9%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-circle);\n    }\n\n    .d-btn__icon {\n        margin: unset;\n    }\n\n    &.d-btn__label {\n        display: none;\n    }\n\n    &.d-btn--outlined {\n        --button--bc: var(--black-700);\n    }\n\n    //  Adjust padding based on sizes\n    &.d-btn--xs {\n        padding: calc(var(--su8) - var(--su1));\n\n        --button--br: var(--br-circle);\n    }\n\n    &.d-btn--sm {\n        padding: var(--su8);\n\n        --button--br: var(--br-circle);\n    }\n\n    &.d-btn--lg {\n        padding: calc(var(--su12) + var(--su1));\n\n        --button--br: var(--br-circle);\n    }\n\n    &.d-btn--xl {\n        padding: calc(var(--su16) - var(--su1));\n\n        --button--br: var(--br-circle);\n    }\n}\n\n//  $$  OUTLINED BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--outlined {\n    --button--bc: var(--primary-color);\n}\n\n//  $$  PRIMARY BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--primary {\n    --button--fc: var(--white);\n    --button--bgc: var(--primary-color);\n\n    &:hover {\n      --button--fc: var(--white);\n      --button--bgc: hsl(var(--primary-color-h), var(--primary-color-s), 65%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n      --button--fc: var(--white);\n      --button--bgc: hsl(var(--primary-color-h), var(--primary-color-s), 42%);\n    }\n}\n\n//  $$  MUTED BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--muted {\n    --button--fc: var(--muted-color);\n\n    &:hover {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 3%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 9%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-muted);\n    }\n\n    &.d-btn--outlined {\n        --button--bc: var(--black-600);\n    }\n}\n\n//  $$  DANGER BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--danger {\n    --button--fc: var(--error-color);\n\n    &:hover {\n        --button--fc: var(--error-color-hover);\n        --button--bgc: hsla(var(--error-color-hsl) ~' / ' 6%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--error-color-hover);\n        --button--bgc: hsla(var(--error-color-hsl) ~' / ' 20%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-error);\n    }\n\n    &.d-btn--outlined {\n        --button--bc: var(--error-color);\n    }\n\n    &.d-btn--primary {\n        --button--fc: var(--white);\n        --button--bgc: var(--error-color);\n\n        &:hover {\n            --button--fc: var(--white);\n            --button--bgc: hsl(var(--red-300-h), var(--red-300-s), 40%);\n        }\n\n        &:active,\n        &.d-btn--active,\n        &.d-btn--active:active {\n            --button--fc: var(--white);\n            --button--bgc: hsl(var(--red-300-h), var(--red-300-s), 27%);\n        }\n    }\n}\n\n//  $$  INVERTED BUTTON\n//  ----------------------------------------------------------------------------\n//  --  Base, clear inverted button\n.d-btn--inverted {\n    --button--fc: var(--white);\n    --button--bgc: transparent;\n\n    &:hover {\n        --button--fc: var(--white);\n        --button--bgc: hsla(var(--white-hsl) ~' / ' 15%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--white);\n        --button--bgc: hsla(var(--white-hsl) ~' / ' 30%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-inverted);\n    }\n\n    //  --  OUTLINED\n    &.d-btn--outlined {\n        --button--bc: var(--white);\n    }\n\n    //  --  PRIMARY\n    &.d-btn--primary {\n        --button--fc: var(--primary-color);\n        --button--bgc: hsl(var(--purple-100-h), var(--purple-100-s), 100%);\n\n        &:hover {\n            --button--fc: var(--primary-color-hover);\n            --button--bgc: hsl(var(--purple-100-h), var(--purple-100-s), 94%);\n        }\n\n        &:active,\n        &.d-btn--active,\n        &.d-btn--active:active {\n            --button--fc: var(--primary-color-hover);\n            --button--bgc: hsl(var(--purple-100-h), var(--purple-100-s), 91%);\n        }\n    }\n}\n\n//  $$  ICON ONLY\n//  ----------------------------------------------------------------------------\n.d-btn--icon-only {\n    padding: calc(var(--su8) + var(--su1));\n\n    .d-btn__icon {\n        margin: unset;\n    }\n}\n\n//  $$  LOADING STATE\n//  ----------------------------------------------------------------------------\n.d-btn--loading {\n    .d-btn-loading();\n}\n\n//  $$  DISABLED STATE\n//  ----------------------------------------------------------------------------\n.d-btn--disabled {\n    color: var(--fc-disabled);\n    background-color: var(--black-300);\n    border-color: transparent;\n    cursor: not-allowed;\n    transition: none;\n    pointer-events: none;\n}\n\n//  $$  BRAND BUTTONS\n//  ----------------------------------------------------------------------------\n.d-btn--brand {\n    display: flex;\n    color: var(--white);\n    background-color: hsl(var(--brand-color-h) var(--brand-color-s) var(--brand-color-l));\n\n    &:hover,\n    &:active {\n        color: hsla(var(--white-hsl) ~' / ' 90%);\n        background-color: hsl(var(--brand-color-h) calc(var(--brand-color-s) + 2.5%) calc(var(--brand-color-l) - 5%));\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su1) var(--white), 0 0 0 0.25em hsla(var(--brand-color-h) var(--brand-color-s) var(--brand-color-l) ~' / ' 90%);\n    }\n\n    &:active {\n        background-color: hsl(var(--brand-color-h) calc(var(--brand-color-s) + 5%) calc(var(--brand-color-l) - 10%));\n    }\n\n    .d-btn__icon {\n      align-items: center;\n      justify-content: center;\n      width: 2em;\n      height: 2em;\n      margin-right: var(--su8);\n      margin-left: var(--sun4);\n      background-color: var(--white);\n      border-radius: var(--br2);\n    }\n\n    .d-btn__label {\n        flex: 1 auto;\n    }\n}\n\n//  --  GOOGLE\n.d-btn--google {\n    --brand-color-h: 217;\n    --brand-color-s: 89%;\n    --brand-color-l: 61%;\n}\n//  --  OFFICE 365\n.d-btn--o365 {\n    --brand-color-h: 15;\n    --brand-color-s: 99%;\n    --brand-color-l: 46%;\n}\n//  --  LINKEDIN\n.d-btn--linkedin {\n    --brand-color-h: 197;\n    --brand-color-s: 100%;\n    --brand-color-l: 26%;\n}\n\n//  ============================================================================\n//  @   BUTTON LOADER\n//      Changes the button\n//  ----------------------------------------------------------------------------\n//  @@  BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-btn-loading() {\n    //  Disable clicking while the loader is in place\n    pointer-events: none;\n\n    //  Hide anything that was there before\n    .d-btn__icon,\n    .d-btn__label {\n        opacity: 0;\n        transition: opacity 50ms var(--ttf-in-out);\n    }\n\n    //  Show the loading animation\n    &::before {\n        position: absolute;\n        width: var(--su16);\n        height: var(--su16);\n        border: var(--su2) solid currentColor;\n        border-left-color: transparent !important;\n        border-radius: var(--br-circle);\n        animation: d-loading-circle 900ms infinite linear;\n        content: '';\n    }\n}\n\n//  ============================================================================\n//  $   BUTTON GROUP\n//  ----------------------------------------------------------------------------\n.d-btn-group {\n    display: flex;\n\n    &.d-btn-group--start {\n        justify-content: start;\n    }\n\n    &.d-btn-group--end {\n        justify-content: end;\n    }\n\n    &.d-btn-group--space-between {\n        justify-content: space-between;\n    }\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: CARD\n//\n//  These are card classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/card\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • HEADER\n//  • CONTENT\n//  • FOOTER\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ============================================================================\n//  $$  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-card {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  background: var(--white);\n  border-radius: var(--su4);\n  box-shadow: var(--bs-card);\n}\n\n\n//  ============================================================================\n//  $   CARD AREAS\n//  ----------------------------------------------------------------------------\n//  $$  HEADER\n//  ----------------------------------------------------------------------------\n.d-card__header {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n  padding: var(--su8) var(--su8) 0 var(--su16);\n}\n\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-card__content {\n  padding: var(--su16);\n  overflow-y: auto;\n}\n\n//  $$  FOOTER\n//  ----------------------------------------------------------------------------\n.d-card__footer {\n  display: flex;\n  align-items: center;\n  padding: var(--su8);\n}\n","//\n//  DIALTONE\n//  COMPONENTS: CHIP\n//\n//  These are chip classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/chip\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • INTERACTIVITY\n//  • SIZES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-chip {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n}\n\n.d-chip__label {\n  //  Component CSS Vars\n  --chip--fc: var(--fc-primary);\n  --chip--bgc: var(--black-200);\n  --chip--br: var(--su102);\n\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  box-sizing: border-box;\n  padding: var(--su4) var(--su8);\n  color: var(--chip--fc);\n  font-size: var(--fs-200);\n  font-family: inherit;\n  line-height: var(--lh4);\n  background-color: var(--chip--bgc);\n  border: none;\n  border-radius: var(--chip--br);\n  transition-timing-function: var(--ttf-in-out);\n  transition-duration: 75ms;\n  transition-property: background-color;\n\n  // Reserves space within the chip for the close button, since the close button is\n  // not nested within the chip. Only apply if close button exists (more than one child).\n  &:not(:only-child)::after {\n    flex-shrink: 0;\n    width: calc(var(--su16) + var(--su2));\n    height: calc(var(--su16) + var(--su2));\n    content: '';\n  }\n\n  // These properties are only set if d-chip is interactive (ex: a button).\n  &:is(a),\n  &:is(button),\n  &:is([role='button']),\n  &:is([role='link']) {\n    text-decoration: none;\n    cursor: pointer;\n\n    &:hover {\n      --chip--bgc: var(--black-300);\n\n      text-decoration: none;\n    }\n\n    &:active {\n      --chip--bgc: var(--black-400);\n    }\n\n    &:focus-visible {\n      outline: none;\n      box-shadow: var(--bs-focus-ring-muted);\n    }\n  }\n\n  .d-avatar {\n    --avatar-size-shape: var(--su24);\n\n    margin: var(--sun2) var(--su4) var(--sun2) var(--sun6);\n  }\n\n  .d-svg {\n    width: @icon18;\n    height: @icon18;\n  }\n}\n\n.d-chip__close {\n  .d-btn();\n  .d-btn--circle();\n\n  position: absolute;\n  right: var(--su2);\n  padding: calc(var(--su2) + var(--su1));\n  border-width: var(--su0);\n\n  &::before {\n    position: absolute;\n    width: 2.6rem;\n    height: 2.8rem;\n    content: '';\n  }\n\n  &:hover {\n    --button--bgc: hsla(var(--black-800-hsl) ~' / ' 15%);\n  }\n\n  &:active {\n    --button--bgc: hsla(var(--black-800-hsl) ~' / ' 25%);\n  }\n\n  .d-btn__icon .d-svg {\n    width: @icon18;\n    height: @icon18;\n  }\n}\n\n.d-chip__icon {\n  padding-right: var(--su4);\n  line-height: 0;\n}\n\n.d-chip__label--active {\n  background-color: var(--black-400);\n}\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n//  $$  EXTRA SMALL\n//  ----------------------------------------------------------------------------\n.d-chip__label--xs {\n  padding: var(--su2) var(--su6);\n  font-size: var(--fs-100);\n\n\n\n  .d-svg {\n    width: @icon14;\n    height: @icon14;\n  }\n\n  // reserves space within the chip for the close button, since the close button is\n  // not nested within the chip.\n  &:not(:only-child)::after {\n    flex-shrink: 0;\n    width: var(--su12);\n    height: var(--su12);\n    content: '';\n  }\n\n  .d-avatar {\n    --avatar-size-shape: var(--su16);\n\n    margin-right: var(--su4);\n    margin-left: var(--sun4);\n  }\n}\n\n.d-chip__close--xs {\n  padding: var(--su1);\n\n  // Clickable area for the close button.\n  &::before {\n    width: var(--su24);\n    height: var(--su24);\n  }\n\n  .d-btn__icon .d-svg {\n    width: @icon14;\n    height: @icon14;\n  }\n}\n\n//  $$ SMALL\n//  ----------------------------------------------------------------------------\n.d-chip__label--sm {\n  padding: var(--su2) var(--su8);\n  font-size: var(--fs-200);\n\n  .d-svg {\n    width: @icon16;\n    height: @icon16;\n  }\n\n  // reserves space within the chip for the close button, since the close button is\n  // not nested within the chip.\n  &:not(:only-child)::after {\n    flex-shrink: 0;\n    width: calc(var(--su12) + var(--su2));\n    height: calc(var(--su12) + var(--su2));\n    content: '';\n  }\n\n  .d-avatar {\n    --avatar-size-shape: calc(var(--su24) - var(--su4));\n\n    margin-right: var(--su4);\n    margin-left: var(--sun6);\n  }\n}\n\n.d-chip__close--sm {\n  padding: var(--su2);\n\n  &::before {\n    width: var(--su24);\n    height: var(--su24);\n  }\n\n  .d-btn__icon .d-svg {\n    width: @icon16;\n    height: @icon16;\n  }\n}\n","//@import (reference) '../variables/typography.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: FORMS\n//\n//  These are form classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/forms\n//\n//  TABLE OF CONTENTS\n//  • FIELDSETS\n//  • LABELS\n//    - Sizes\n//  • DESCRIPTIONS\n//    - Sizes\n//  • VALIDATION MESSAGES\n//    - Validation States\n//\n//  ============================================================================\n//  $  FIELDSETS\n//  ----------------------------------------------------------------------------\nfieldset {\n  min-width: var(--su0);\n  padding: var(--su0);\n  border: none;\n}\n\n\n//  ============================================================================\n//  $  LABELS\n//  ----------------------------------------------------------------------------\n.d-label {\n  display: flex;\n  flex: 1 0%;\n  align-items: baseline;\n  justify-content: space-between;\n  box-sizing: border-box;\n  margin-bottom: var(--su6);\n  color: currentColor;\n  font-weight: @fw-bold;\n  font-size: var(--fs-200);\n  font-family: inherit;\n  line-height: var(--lh4);\n  word-break: break-word;\n  overflow-wrap: break-word;\n  cursor: pointer;\n\n  legend & { cursor: default; }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-label--xs {\n  font-size: var(--fs-100);\n  line-height: var(--lh4);\n}\n\n.d-label--sm {\n  font-size: var(--fs-200);\n  line-height: var(--lh2);\n}\n\n.d-label--lg {\n  font-size: var(--fs-200);\n  line-height: var(--lh2);\n}\n\n.d-label--xl {\n  font-size: var(--fs-300);\n  line-height: var(--lh4);\n}\n\n\n//  ============================================================================\n//  $ DESCRIPTIONS\n//  ----------------------------------------------------------------------------\n.d-description {\n  display: flex;\n  box-sizing: border-box;\n  margin-bottom: var(--su6);\n  color: var(--black-600);\n  font-size: var(--fs-100);\n  font-family: inherit;\n  line-height: var(--lh4);\n  fill: currentColor;\n}\n\n.d-label + .d-description {\n  margin-top: var(--sun6);\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-description--lg {\n  font-size: var(--fs-200);\n  line-height: var(--lh6);\n}\n\n.d-description--xl {\n  font-size: var(--fs-200);\n  line-height: var(--lh6);\n}\n\n//  ============================================================================\n//  $   VALIDATION MESSAGES\n//  ----------------------------------------------------------------------------\n.d-validation-message {\n  --validation--fc: var(--fc-tertiary);\n\n  display: inline-flex;\n  align-items: flex-start;\n  margin-top: var(--su6);\n  color: var(--validation--fc);\n  font-weight: inherit;\n  font-size: var(--fs-100);\n  font-family: inherit;\n  line-height: var(--lh4);\n\n  // Icon Slot\n  &::before {\n    width: var(--su16);\n    min-width: var(--su16);\n    height: var(--su16);\n    margin-right: var(--su4);\n    content: '';\n  }\n}\n\n//  $$   VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-validation-message--warning {\n  --validation--fc: var(--fc-warning);\n\n  &::before {\n    background-image: url(\"data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 21h22L12 2 1 21zm12-3h-2v-2h2v2zm0-4h-2v-4h2v4z' fill='hsl(43 100% 33%)'/%3E%3C/svg%3E\");\n    background-repeat: no-repeat;\n  }\n}\n\n.d-validation-message--error {\n  --validation--fc: var(--fc-error);\n\n  &::before {\n    background-image: url(\"data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-2h2v2zm0-4h-2V7h2v6z' fill='hsl(0 100% 29%)'/%3E%3C/svg%3E\");\n    background-repeat: no-repeat;\n  }\n}\n\n.d-validation-message--success {\n  --validation--fc: var(--fc-success);\n\n  &::before {\n    background-image: url(\"data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z' fill='hsl(115 94% 18%)'/%3E%3C/svg%3E\");\n    background-repeat: no-repeat;\n  }\n}\n","//@import (reference) '../variables/icons.less';\n//@import (reference) '../variables/sizes.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: INPUTS\n//\n//  These are input classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/inputs\n//\n//  TABLE OF CONTENTS\n//  • VARIABLES\n//  • INPUTS & TEXTAREAS\n//    - Base Style\n//    - Wrapper\n//    - Validation States\n//    - Sizes\n//  • INPUT ICONS\n//\n\n//  $  INPUTS\n//  ----------------------------------------------------------------------------\n.d-input,\n.d-textarea,\n.d-input__wrapper {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --input--focus-bc: var(--primary-color);\n    --input--bc: var(--black-600);\n    --input--bgc: var(--white);\n    --input--fc: var(--black-800);\n\n    position: relative;\n    display: inline-flex;\n    flex: 1 0%;\n    box-sizing: border-box;\n    width: 100%;\n    min-width: 0;\n    padding: 0.7rem var(--su8);\n    color: var(--input--fc);\n    font-weight: inherit;\n    font-size: var(--fs-200);\n    font-family: inherit;\n    line-height: var(--lh4);\n    background-color: var(--input--bgc);\n    border: 1px solid var(--input--bc);\n    border-radius: var(--base--corner-radius);\n    outline: none;\n    box-shadow: none;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: 100ms;\n    transition-property: border, box-shadow, background-color;\n\n    //  --  Placeholder copy\n    &::placeholder {\n        color: var(--fc-placeholder);\n    }\n    //  --  Remove input EDGE additions\n    &::-ms-clear {\n        display: none;\n    }\n    //  --  FOCUS\n    &:focus,\n    &:focus-within {\n        --input--bc: var(--input--focus-bc);\n\n        outline: 0;\n        box-shadow: var(--bs-focus-ring) !important;\n    }\n    //  --  DISABLED / READ-ONLY\n    &[disabled],\n    &[read-only] {\n        --input--bc: var(--black-400) !important;\n        --input--bgc: var(--black-300);\n        --input--fc: var(--fc-disabled);\n\n        &:focus,\n        &:focus-within {\n            box-shadow: none !important;\n        }\n\n        //  --  Placeholder copy\n        &::placeholder {\n            color: var(--fc-placeholder);\n        }\n    }\n    //  --  DISABLED\n    &[disabled] {\n        cursor: not-allowed;\n    }\n}\n\n//  $$  INPUT WRAPPER\n//  ----------------------------------------------------------------------------\n.d-input__wrapper {\n    padding: 0;\n\n    .d-input-icon.d-input-icon--right {\n        margin-right: var(--su8);\n    }\n\n    .d-input-icon.d-input-icon--left {\n        margin-left: var(--su8);\n    }\n\n    .d-input,\n    .d-textarea {\n        flex: 1;\n        border: none;\n        border-radius: var(--base--corner-radius);\n\n        &:focus {\n            border: none;\n            outline: 0;\n            box-shadow: none !important;\n        }\n\n        &.d-input-icon--right {\n            padding-right: var(--su6);\n        }\n\n        &.d-input-icon--left {\n            padding-left: var(--su6);\n        }\n    }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-input.d-input--xs {\n    .input-button-xs();\n}\n\n.d-input.d-input--sm {\n    .input-button-sm();\n}\n\n.d-input.d-input--lg {\n    .input-button-lg();\n}\n\n.d-input.d-input--xl {\n    .input-button-xl();\n}\n\n\n//  ============================================================================\n//  $  TEXTAREAS\n//  ----------------------------------------------------------------------------\n.d-textarea {\n    min-height: 8rem;\n    vertical-align: top;\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-textarea--xs {\n    .input-button-xs();\n\n    min-height: 4rem;\n}\n\n.d-textarea--sm {\n    .input-button-sm();\n\n    min-height: 4.8rem;\n}\n\n.d-textarea--lg {\n    .input-button-lg();\n\n    min-height: 9.2rem;\n}\n\n.d-textarea--xl {\n    .input-button-xl();\n\n    min-height: 10rem;\n}\n\n//  $$  VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-input--warning,\n.d-textarea--warning {\n    --input--bc: var(--warning-color) !important;\n    --input--focus-bc: var(--warning-color);\n\n    &:focus,\n    &:focus-within {\n        box-shadow: var(--bs-focus-ring-warning) !important;\n    }\n}\n\n.d-input--error,\n.d-textarea--error {\n    --input--bc: var(--error-color) !important;\n    --input--focus-bc: var(--error-color);\n\n    &:focus,\n    &:focus-within {\n        box-shadow: var(--bs-focus-ring-error) !important;\n    }\n}\n\n.d-input--success,\n.d-textarea--success {\n    --input--bc: var(--success-color) !important;\n    --input--focus-bc: var(--success-color);\n\n    &:focus,\n    &:focus-within {\n        box-shadow: var(--bs-focus-ring-success) !important;\n    }\n}\n\n//  ============================================================================\n//  $  INPUT ICONS\n//  ----------------------------------------------------------------------------\n.d-input-icon {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --input-icon-size: @icon16;\n\n    z-index: var(--zi-base1);\n    display: inline-flex;\n    align-items: center;\n    height: 3.6rem;\n    margin: 0;\n    line-height: 0;\n\n    svg {\n        width: var(--input-icon-size);\n        height: var(--input-icon-size);\n    }\n}\n\n\n\n//  $$ SIZES\n//  ----------------------------------------------------------------------------\n.d-input-icon.d-input--xs {\n    height: 2.8rem;\n}\n\n.d-input-icon--xs {\n    --input-icon-size: @icon12;\n}\n\n.d-input-icon.d-input--sm {\n    height: 3.2rem;\n}\n\n.d-input-icon--sm {\n    --input-icon-size: @icon14;\n}\n\n.d-input-icon.d-input--lg {\n    height: 4.8rem;\n}\n\n.d-input-icon--lg {\n    --input-icon-size: @icon20;\n}\n\n.d-input-icon.d-input--xl {\n    height: 5.6rem;\n}\n\n.d-input-icon--xl {\n    --input-icon-size: @icon24;\n}\n","//\n//  DIALTONE\n//  TOKENS: SIZES\n//\n//  These are shared sizes for buttons, inputs, select menus (native),\n//  and textareas.\n//\n//  ============================================================================\n//  $   CSS VARS\n//  ============================================================================\n@input-button-vars: {\n    input-button__padding:                  0.7rem var(--su8);\n    input-button__text-size:                var(--fs-200);\n    input-button__line-height:              var(--lh4);\n    input-button__icon-size:                @icon-size18;\n}\n\n//  ============================================================================\n//  $   SHARED INPUT & BUTTON SIZES\n//  ============================================================================\n//  $$  EXTRA SMALL\n//      Target height: 24px\n//  ----------------------------------------------------------------------------\n.input-button-xs() {\n    padding-top: 0.5rem;\n    padding-bottom: 0.5rem;\n    font-size: var(--fs-100);\n\n    .d-btn__icon {\n        width: @icon-size14;\n        height: @icon-size14;\n    }\n}\n\n//  $$  SMALL\n//      Target height: 32px\n//  ----------------------------------------------------------------------------\n.input-button-sm() {\n    padding-top: var(--su6);\n    padding-bottom: var(--su6);\n    font-size: var(--fs-200);\n\n    .d-btn__icon {\n        width: @icon-size16;\n        height: @icon-size16;\n    }\n}\n\n//  $$  LARGE\n//      Target height: 48px\n//  ----------------------------------------------------------------------------\n.input-button-lg() {\n    padding-top: 1.1rem;\n    padding-bottom: 1.1rem;\n    font-size: var(--fs-300);\n\n    .d-btn__icon {\n        width: @icon-size20;\n        height: @icon-size20;\n    }\n}\n\n//  $$  EXTRA LARGE\n//      Target height: 56px\n//  ----------------------------------------------------------------------------\n.input-button-xl() {\n    padding-top: 1.3rem;\n    padding-bottom: 1.3rem;\n    font-size: var(--fs-300);\n\n    .d-btn__icon {\n        width: @icon-size24;\n        height: @icon-size24;\n    }\n}\n\n#d-internals #spacing-classes(0.8rem, 'size');\n#d-internals #spacing-classes(1.6rem, 'size', '-16');\n#d-internals #spacing-classes(3.2rem, 'size', '-32');\n#d-internals #spacing-classes(6.4rem, 'size', '-64');\n#d-internals #spacing-classes(12.8rem, 'size', '-128');\n","//\n//  DIALTONE\n//  COMPONENTS: LINKS\n//\n//  These are link classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/links\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • ALTERNATE STYLES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-link {\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    margin: var(--su0);\n    padding: var(--su0);\n    color: var(--primary-color);\n    font: inherit;\n    text-decoration: underline;\n    background-color: transparent;\n    border: 0;\n    transition: all var(--td25) var(--ttf-in-out);\n\n    //  Reset button appearance\n    -webkit-appearance: none;\n       -moz-appearance: none;\n            appearance: none;\n    fill: currentColor;\n\n    &:hover {\n        color: var(--primary-color-hover);\n        text-decoration: none;\n        cursor: pointer;\n    }\n\n    &:focus-visible {\n        border-radius: var(--br4);\n        outline: none;\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring);\n    }\n}\n\n\n//  ============================================================================\n//  $   ALTERNATE STYLES\n//  ============================================================================\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-link--warning {\n    color: var(--fc-warning);\n\n    &:hover {\n        color: var(--fc-warning-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-warning);\n    }\n}\n\n//  $$  DANGER\n//  ----------------------------------------------------------------------------\n.d-link--danger {\n    color: var(--fc-error);\n\n    &:hover {\n        color: var(--error-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-error);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-link--success {\n    color: var(--fc-success);\n\n    &:hover {\n        color: var(--success-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-success);\n    }\n}\n\n//  $$  MUTED\n//  ----------------------------------------------------------------------------\n.d-link--muted {\n    color: var(--muted-color);\n\n    &:hover {\n        color: var(--muted-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-muted);\n    }\n}\n\n//  $$  DISABLED\n//  ----------------------------------------------------------------------------\n// We don't expose these in the documentation because they're meant to be consumed\n// by buttons using .d-link. Links with a disabled attribute are not valid mark-up.\n.d-link[disabled],\n.d-link--disabled {\n    color: var(--fc-disabled);\n\n    &:hover {\n        color: var(--fc-disabled);\n        text-decoration: underline;\n        cursor: not-allowed;\n    }\n\n    &:focus {\n        box-shadow: none;\n    }\n}\n\n//  $$  INVERTED\n//  ----------------------------------------------------------------------------\n.d-link--inverted {\n    color: var(--inverted-color);\n\n    &:hover {\n        color: var(--inverted-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-inverted);\n    }\n}\n\n//  $$  INVERTED DISABLED\n//  ----------------------------------------------------------------------------\n// We don't expose these in the documentation because they're meant to be consumed\n// by buttons using .d-link. Links with a disabled attribute are not valid mark-up.\n.d-link--inverted-disabled {\n    color: var(--black-500);\n\n    &:hover {\n        color: var(--black-500);\n        text-decoration: underline;\n        cursor: not-allowed;\n    }\n\n    &:focus {\n        box-shadow: none;\n    }\n}\n","//\n//  DIALTONE 5 SHIM\n//  COMPONENTS: LIST GROUPS\n//\n//  These are the list group definitions, which are used by classes or can be\n//  used within Vue components. For further documentation of these values,\n//  please visit https://dialpad.design/components/list-groups\n//\n//  ============================================================================\n.d-list-group,\n.d-list-group__item {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n}\n\n.d-list-group--header,\n.d-list-group--link {\n    display: block;\n    padding: var(--su4) var(--su24);\n}\n\n.d-list-group--header {\n    font-weight: var(--fw-bold);\n    font-size: var(--fs-100);\n    text-transform: uppercase;\n}\n\n.d-list-group--link {\n    color: var(--black-800);\n    font-size: var(--fs-200);\n    text-decoration: none;\n\n    &:hover {\n        color: var(--black-900);\n        background-color: hsla(var(--primary-color-hsl) ~' / ' 85%);\n    }\n\n    &-selected {\n        color: var(--white);\n        background-color: var(--primary-color);\n\n        &:hover {\n            color: var(--white);\n            background-color: hsl(var(--primary-color-h) var(--primary-color-s) calc(var(--primary-color-l) + 10%));\n        }\n    }\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: MODALS\n//\n//  These are modal classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/modals\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLES\n//    - Wrapper\n//    - Dialog\n//    - Make them appear\n//  • MODAL BACKGROUND\n//  • HEADER\n//  • CONTENT\n//  • FOOTER\n//  • TRANSITIONS\n//\n//  ============================================================================\n//  $   BASE STYLES\n//  ============================================================================\n//  $$  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-modal {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --modal--bgc: hsla(var(--black-900-hsl) ~' / ' 60%);\n    --modal-dialog--bgc: var(--white);\n    --modal-dialog--fc: var(--black-700);\n    --modal-header--fc: var(--black-800);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: var(--zi-hide);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    padding: var(--su32);\n    background-color: var(--modal--bgc);\n    backface-visibility: hidden;\n    visibility: hidden;\n    opacity: 0;\n    will-change: visibility, z-index, opacity;\n}\n\n.d-modal--transparent {\n  //  Component CSS Vars\n  //  ------------------------------------------------------------------------\n  --modal--bgc: hsla(var(--black-900-hsl) ~' / ' 85%);\n  --modal-dialog--bgc: var(--white);\n  --modal-dialog--fc: var(--black-700);\n  --modal-header--fc: var(--black-800);\n\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: var(--zi-hide);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: var(--su32);\n  background-color: var(--d-bgc-transparent);\n  backface-visibility: hidden;\n  visibility: hidden;\n  opacity: 0;\n  will-change: visibility, z-index, opacity;\n}\n\n//  $$  MODAL DIALOG\n//  ----------------------------------------------------------------------------\n.d-modal__dialog {\n  position: relative;\n  z-index: var(--zi-hide);\n  width: 100%;\n  max-width: var(--size628);\n  max-height: 100%;\n  overflow-y: auto;\n  color: var(--modal-dialog--fc);\n  font-size: var(--fs-200);\n  line-height: var(--lh8);\n  background-color: var(--modal-dialog--bgc);\n  border-radius: var(--br8);\n  box-shadow: var(--bs-card);\n  transform: translate3d(0, 30%, 0) scale3d(0.75, 0.75, 0.75);\n  backface-visibility: hidden;\n  visibility: hidden;\n  opacity: 0;\n  will-change: visibility, z-index, opacity, transform;\n}\n\n//  $$   MAKE THEM APPEAR\n//  ----------------------------------------------------------------------------\n.d-modal[aria-hidden='false'],\n.d-modal[aria-hidden='false'] .d-modal__dialog,\n.d-modal--transparent[aria-hidden='false'],\n.d-modal--transparent[aria-hidden='false'] .d-modal__dialog {\n  z-index: var(--zi-modal);\n  transform: translate3d(0, 0, 0) scale3d(1, 1, 1);\n  visibility: visible;\n  opacity: 1;\n}\n\n\n//  ============================================================================\n//  $   MODAL AREAS\n//  ----------------------------------------------------------------------------\n.d-modal__footer {\n  display: flex;\n  flex-direction: row-reverse;\n  align-items: center;\n  padding: 0 var(--su24) var(--su24) var(--su24);\n\n  >:not(:first-child) {\n    margin-right: var(--su4);\n  }\n\n  >:not(:last-child) {\n    margin-left: var(--su4);\n  }\n}\n\n\n//  ============================================================================\n//  $   TYPE STYLES\n//  ============================================================================\n//  $$  HEADER\n//  ----------------------------------------------------------------------------\n.d-modal__header {\n  margin: 0 !important;\n  padding: var(--su24) var(--su24) 0;\n  color: var(--modal-header--fc);\n  font-weight: var(--fw-bold);\n  font-size: var(--fs-300);\n  line-height: var(--lh4);\n}\n\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-modal__content {\n  max-width: 75ch;\n  margin: var(--su12) 0;\n  padding: var(--su4) var(--su24);\n}\n\n\n//  ============================================================================\n//  $   BUTTONS\n//  ----------------------------------------------------------------------------\n.d-modal__close {\n  position: absolute;\n  top: var(--su8);\n  right: var(--su8);\n  margin: 0 !important;\n}\n\n\n//  ============================================================================\n//  $   BANNERS\n//  ----------------------------------------------------------------------------\n.d-modal__banner {\n  position: relative;\n  box-sizing: border-box;\n  width: 100%;\n  max-width: var(--size628);\n  padding: var(--su12) var(--su24);\n  font-size: var(--fs-200);\n  line-height: var(--lh4);\n  background-color: var(--gold-100);\n  border-radius: var(--br8) var(--br8) 0 0;\n\n  &:not(.d-d-none) + .d-modal__dialog {\n    border-top-left-radius: 0;\n    border-top-right-radius: 0;\n  }\n}\n\n\n//  ============================================================================\n//  $   MODIFYING STYLES\n//  ============================================================================\n//  $$  FULL WIDTH\n//  ----------------------------------------------------------------------------\n.d-modal--full {\n  padding: 0;\n  overflow: hidden scroll;\n  transform: unset !important;\n\n  .d-modal__dialog {\n    display: flex;\n    flex-direction: column;\n    max-width: unset;\n    height: 100%;\n    border-radius: 0;\n    transform: unset !important;\n  }\n\n  .d-modal__header {\n    padding: var(--su32) var(--su32) 0;\n  }\n\n  .d-modal__content {\n    padding-right: 0;\n    padding-left: var(--su32);\n  }\n\n  .d-modal__footer {\n    margin-top: auto !important;\n    padding: 0 var(--su24) var(--su32) var(--su32);\n  }\n\n  .d-modal__banner {\n    max-width: unset;\n    padding: var(--su12) var(--su32);\n    border-radius: 0;\n  }\n}\n\n// $$   SCROLLABLE\n//  ----------------------------------------------------------------------------\n.d-modal__dialog--scrollable {\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n\n  .d-modal__content {\n    overflow-y: auto;\n  }\n}\n\n//  $$  DANGER\n//  ----------------------------------------------------------------------------\n.d-modal--danger {\n  --modal-header--fc: var(--fc-dark);\n}\n\n\n//  ============================================================================\n//  $   TRANSITIONS\n//  ----------------------------------------------------------------------------\n.d-modal--animate {\n  z-index: var(--zi-hide) !important;\n  backface-visibility: hidden !important;\n  visibility: hidden !important;\n  opacity: 0 !important;\n}\n\n.d-modal__dialog--animate {\n  .d-modal--animate();\n\n  transform: translate3d(0, 30%, 0) scale3d(0.75, 0.75, 0.75) !important;\n}\n\n.d-modal--animate-in,\n .d-modal__dialog--animate-in {\n  transition:\n    opacity 100ms var(--ttf-in-out) 10ms,\n    z-index 0s 0s,\n    visibility 0s 0s,\n    transform 100ms var(--ttf-in-out) 10ms;\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n\n.d-modal--animate-out {\n  transition:\n    opacity 100ms var(--ttf-in-out) 10ms,\n    z-index 0s 200ms,\n    visibility 0s 200ms,\n    transform 100ms var(--ttf-in-out) 10ms;\n\n  &.d-modal--full {\n    transition:\n      opacity 200ms var(--ttf-in-out) 0s,\n      z-index 0s 200ms,\n      visibility 0s 200ms;\n  }\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n\n.d-modal__dialog--animate-out {\n  transition:\n    opacity 200ms var(--ttf-in-out) 0s,\n    z-index 0s 200ms,\n    visibility 0s 200ms,\n    transform 100ms var(--ttf-in-out) 0s;\n\n  &.d-modal--full {\n    transition:\n      opacity 200ms var(--ttf-in-out) 0s,\n      z-index 0s 200ms,\n      visibility 0s 200ms;\n  }\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n","//@import (reference) '../utilities/spacing.less';\n//@import (reference) '../utilities/effects.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TOAST\n//\n//  These are the styles for toast notices, which are used by alert users to\n//  information they might need. For further documentation of these styles,\n//  please visit https://dialpad.design/components/toasts\n\n//  ============================================================================\n//  @   TOAST CONTAINER\n//  ----------------------------------------------------------------------------\n.d-toast-wrapper {\n    position: absolute;\n    top: var(--su32);\n    left: 50%;\n    transform: translateX(-50%);\n\n    .d-stack8();\n}\n\n//  ============================================================================\n//  @   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-toast {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --toast--bgc: var(--black-100);\n    --toast--fc: var(--fc-primary);\n\n    z-index: var(--zi-notification);\n    box-sizing: border-box;\n    width: 100%;\n    max-width: var(--size628);\n    margin-bottom: var(--su8);\n    padding: var(--su12);\n    color: var(--toast--fc);\n    font-size: var(--fs-200);\n    line-height: var(--lh6);\n    word-break: normal;\n    background-color: var(--toast--bgc);\n    border-radius: var(--br4);\n    .d-bs-md();\n\n    //  If there's a link in a toast, break the url so it wraps\n    > a {\n        word-break: break-all;\n    }\n\n    // If you want to hide and reveal the toast\n    &[aria-hidden='true'] {\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-toast__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n\n    .d-notice__content {\n        margin-right: var(--su16);\n    }\n\n    .d-notice__actions {\n        margin-left: var(--su32);\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-toast.d-toast--important {\n    --toast--bgc: var(--black-800);\n    --toast--fc: var(--black-100);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-toast--error {\n    --toast--bgc: var(--red-100);\n    --toast--fc: var(--red-500);\n\n    &.d-toast--important {\n        --toast--bgc: var(--red-300);\n        --toast--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-toast--info {\n    --toast--bgc: var(--purple-100);\n    --toast--fc: var(--fc-primary);\n\n    &.d-toast--important {\n        --toast--bgc: var(--purple-400);\n        --toast--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-toast--success {\n    --toast--bgc: var(--green-100);\n    --toast--fc: var(--green-500);\n\n    &.d-toast--important {\n        --toast--bgc: var(--green-200);\n        --toast--fc: var(--green-500);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-toast--warning {\n    --toast--bgc: var(--gold-100);\n\n    &.d-toast--important {\n        --toast--bgc: var(--gold-200);\n        --toast--fc: var(--fc-primary);\n    }\n}\n\n//  ============================================================================\n//  $   UC Toast Shims\n//  ============================================================================\n//  $$  CHAT\n//  ----------------------------------------------------------------------------\n.d-toast--chat {\n  .d-notice__icon,\n .d-toast__meta {\n    color: var(--black-600);\n  }\n\n  .d-toast__meta {\n    font-size: var(--fs-100);\n  }\n\n  &.d-toast--important {\n    .d-notice__icon,\n .d-toast__meta {\n      color: var(--black-100);\n    }\n  }\n}\n\n//  $$  ORGANIZER\n//  ----------------------------------------------------------------------------\n.d-toast--organizer {\n  //\n}\n\n//  $$  VIEWING\n//  ----------------------------------------------------------------------------\n.d-toast--viewing {\n //\n}\n\n.d-toast--organizer,\n.d-toast--viewing {\n  border-top: var(--su4) solid var(--magenta-300);\n\n  .d-notice__icon {\n    color: var(--magenta-300);\n  }\n}\n\n.d-toast--chat,\n.d-toast--organizer,\n.d-toast--viewing {\n  --toast--bgc: var(--white);\n\n  .d-notice__icon {\n    align-self: flex-start;\n  }\n\n  .d-notice__message {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n  }\n}\n","//@import (reference) '../variables/spacing.less';\n\n//\n//  DIALTONE\n//  UTILITIES: EFFECTS\n//\n//  These are effects utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/effects\n//\n//  TABLE OF CONTENTS\n//  • ANIMATIONS\n//      - GENERIC LOADING\n//      - DIALPAD BAR ANIMATION\n//  • BOX SHADOW\n//  • OPACITY\n//  • TRANSITION\n//\n//\n//  ============================================================================\n//  $   ANIMATIONS\n//  ----------------------------------------------------------------------------\n//  $$  GENERIC LOADING\n//      This could be applied to an empty div or a psuedo element (ie ::before)\n//  ----------------------------------------------------------------------------\n@keyframes d-loading-circle {\n    from {\n        transform: rotate(0deg);\n    }\n\n    to {\n        transform: rotate(360deg);\n    }\n}\n\n//  $$   DIALPAD BAR ANIMATION\n//  ----------------------------------------------------------------------------\n@keyframes d-loading-bars__short {\n  0% {\n    min-height: @su4;\n    transform: translateY(-@su16);\n    opacity: 0;\n  }\n\n  5% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0.5;\n  }\n\n  10% {\n    min-height: 1rem;\n    transform: translateY(-@su2);\n    opacity: 1;\n  }\n\n  15% {\n    transform: translateY(@su1);\n    opacity: 1;\n  }\n\n  20%,\n 70% {\n    min-height: 1rem;\n    transform: translateY(0);\n    opacity: 1;\n  }\n\n  75% {\n    min-height: 0.75rem;\n    transform: translateY(1rem);\n    opacity: 1;\n  }\n\n  80% {\n    min-height: @su4;\n    transform: translateY(-@su4);\n    opacity: 0.5;\n  }\n\n  85%,\n 100% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0;\n  }\n}\n\n@keyframes d-loading-bars__tall {\n  0% {\n    min-height: @su4;\n    transform: translateY(-@su16);\n    opacity: 0;\n  }\n\n  5% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0.5;\n  }\n\n  10% {\n    min-height: 1rem;\n    transform: translateY(-@su2);\n    opacity: 1;\n  }\n\n  15% {\n    min-height: 2.2rem;\n    transform: translateY(@su1);\n    opacity: 1;\n  }\n\n  20%,\n 70% {\n    min-height: 2.2rem;\n    transform: translateY(0);\n    opacity: 1;\n  }\n\n  75% {\n    min-height: @su16;\n    transform: translateY(1rem);\n    opacity: 1;\n  }\n\n  80% {\n    min-height: 1rem;\n    transform: translateY(-@su4);\n    opacity: 1;\n  }\n\n  85%,\n 100% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0;\n  }\n}\n\n//  ============================================================================\n//  $   BOX SHADOW\n//  ----------------------------------------------------------------------------\n.d-bs-sm { box-shadow: var(--bs-sm) !important; }\n#d-internals #generate-hover-focus(d-bs-sm, {.d-bs-sm();});\n.d-bs-md { box-shadow: var(--bs-md) !important; }\n#d-internals #generate-hover-focus(d-bs-md, {.d-bs-md ();});\n.d-bs-lg { box-shadow: var(--bs-lg) !important; }\n#d-internals #generate-hover-focus(d-bs-lg, {.d-bs-lg();});\n.d-bs-xl { box-shadow: var(--bs-xl) !important; }\n#d-internals #generate-hover-focus(d-bs-xl, {.d-bs-xl();});\n.d-bs-card { box-shadow: var(--bs-card) !important; }\n#d-internals #generate-hover-focus(d-bs-card, {.d-bs-card();});\n.d-bs-none { box-shadow: none !important; }\n#d-internals #generate-hover-focus(d-bs-none, {.d-bs-none();});\n.d-bs-unset { box-shadow: unset !important; }\n#d-internals #generate-hover-focus(d-bs-unset, {.d-bs-unset();});\n\n\n//  ============================================================================\n//  $   OPACITY\n//      For times that you want a fill background, but not at 100% opacity.\n//      These are only offered for $white and gray.\n//  ----------------------------------------------------------------------------\n.d-o0 { opacity: 0 !important; }\n.d-o5 { opacity: 0.05 !important; }\n.d-o10 { opacity: 0.1 !important; }\n.d-o20 { opacity: 0.2 !important; }\n.d-o25 { opacity: 0.25 !important; }\n.d-o30 { opacity: 0.3 !important; }\n.d-o40 { opacity: 0.4 !important; }\n.d-o50 { opacity: 0.5 !important; }\n.d-o60 { opacity: 0.6 !important; }\n.d-o70 { opacity: 0.7 !important; }\n.d-o75 { opacity: 0.75 !important; }\n.d-o80 { opacity: 0.8 !important; }\n.d-o90 { opacity: 0.9 !important; }\n.d-o100 { opacity: 1 !important; }\n.d-o-unset { opacity: unset !important; }\n\n\n//  ============================================================================\n//  $   TRANSITION\n//  ----------------------------------------------------------------------------\n.d-t {\n    transition-delay: 0s;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: var(--td50);\n    transition-property: all;\n}\n\n//  --  TRANSITION DURATION\n.d-td0 { transition-duration: var(--td0) !important; }\n.d-td50 { transition-duration: var(--td50) !important; }\n.d-td100 { transition-duration: var(--td100) !important; }\n.d-td150 { transition-duration: var(--td150) !important; }\n.d-td200 { transition-duration: var(--td200) !important; }\n\n//  --  TRANSITION TIMING\n.d-ttf-in-out { transition-timing-function: var(--ttf-in-out) !important; }\n.d-ttf-out { transition-timing-function: var(--ttf-out) !important; }\n\n//  --  TRANSITION PROPERTY\n.d-tp-all { transition-property: all !important; }\n.d-tp-o { transition-property: opacity !important; }\n.d-tp-bs { transition-property: box-shadow !important; }\n.d-tp-bgc { transition-property: background-color !important; }\n.d-tp-transform { transition-property: transform !important; }\n.d-tp-colors { transition-property: background-color, border-color, color, fill, stroke !important; }\n\n//  --  TRANSITION DELAY\n.d-t-delay25 { transition-delay: var(--td25) !important; }\n.d-t-delay50 { transition-delay: var(--td50) !important; }\n.d-t-delay100 { transition-delay: var(--td100) !important; }\n.d-t-delay150 { transition-delay: var(--td150) !important; }\n.d-t-delay200 { transition-delay: var(--td200) !important; }\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: NOTICES\n//\n//  These are the styles for notices, which are used by alert users to\n//  information they might need. For further documentation of these styles,\n//  please visit https://dialpad.design/components/notices\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n\n.d-notice {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --notice--bgc: var(--black-100);\n    --notice--fc: var(--fc-primary);\n\n    display: flex;\n    align-items: center;\n\n    //  Base Styles\n    //  ------------------------------------------------------------------------\n    box-sizing: border-box;\n    width: 100%;\n    max-width: var(--size628);\n    padding: var(--su12);\n    color: var(--notice--fc);\n    font-size: var(--fs-200);\n    line-height: var(--lh6);\n    background-color: var(--notice--bgc);\n    border-radius: var(--br4);\n\n    .d-toast & {\n        max-width: var(--size464);\n        box-shadow: var(--bs-sm);\n        pointer-events: all;\n    }\n}\n\n//  ============================================================================\n//  $   NOTICE AREAS\n//  ============================================================================\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-notice__content {\n    display: flex;\n    flex: 1 auto;\n    flex-direction: column;\n    margin-right: var(--su16);\n}\n\n//  $$  ACTIONS\n//  ----------------------------------------------------------------------------\n.d-notice__actions {\n    display: flex;\n    flex: 0 auto;\n    align-items: center;\n\n    .d-flow8();\n}\n\n//  $$  ICON\n//  ----------------------------------------------------------------------------\n.d-notice__icon {\n    display: flex;\n    flex: 0 auto;\n    margin-right: var(--su8);\n    color: inherit;\n}\n\n//  ============================================================================\n//  $   COPY STYLES\n//  ============================================================================\n.d-notice__title {\n    font-weight: var(--fw-bold);\n    font-size: inherit;\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-notice.d-notice--important {\n    --notice--bgc: var(--black-800);\n    --notice--fc: var(--black-100);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-notice--error,\n.d-banner--error,\n.d-toast--error {\n    --notice--bgc: var(--red-100);\n    --notice--fc: var(--red-500);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--red-300);\n        --notice--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-notice--info,\n.d-banner--info,\n.d-toast--info {\n    --notice--bgc: var(--purple-100);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--purple-400);\n        --notice--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-notice--success,\n.d-banner--success,\n.d-toast--success {\n    --notice--bgc: var(--green-100);\n    --notice--fc: var(--green-500);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--green-200);\n        --notice--fc: var(--green-500);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-notice--warning,\n.d-banner--warning,\n.d-toast--warning {\n    --notice--bgc: var(--gold-100);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--gold-200);\n        --notice--fc: var(--fc-primary);\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: POPOVER\n//\n//  These are popover classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/popover\n//\n//  TABLE OF CONTENTS\n//  • POPOVER\n//  • POPOVER DIALOG\n//    - Base dialog style\n//    - Content\n//    - Header / Footer\n\n\n//  $  POPOVER\n//  ----------------------------------------------------------------------------\n\n//  $  POPOVER DIALOG\n//  ----------------------------------------------------------------------------\n.d-popover__dialog {\n  &,\n  *,\n  *::before,\n  *::after {\n    box-sizing: border-box;\n  }\n\n  display: grid;\n  grid-template-rows: auto 1fr;\n  overflow: auto;\n  background-color: var(--white);\n  border-color: var(--black-400);\n  border-radius: var(--su8);\n  box-shadow: var(--bs-card);\n}\n\n// to be set on the dialog when it is modal\n.d-popover__dialog--modal {\n  z-index: var(--zi-modal-element);\n}\n\n//  $$  DIALOG CONTENT\n//  ----------------------------------------------------------------------------\n.d-popover__content {\n  overflow: auto;\n}\n\n//  $$  DIALOG HEADER / FOOTER\n//  ----------------------------------------------------------------------------\n.d-popover__header-footer-base {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n  width: 100%;\n  min-height: var(--size48);\n  padding-top: var(--su6);\n  padding-bottom: var(--su6);\n  overflow: auto;\n  font-weight: var(--fw-bold);\n  font-size: var(--fs-200);\n}\n\n.d-popover__header {\n  .d-popover__header-footer-base();\n\n  border-bottom: var(--su1) solid var(--black-300);\n}\n\n.d-popover__footer {\n  .d-popover__header-footer-base();\n\n  border-top: var(--su1) solid var(--black-300);\n}\n","//\n//  DIALTONE\n//  COMPONENTS: RADIOS & CHECKBOXES\n//\n//  These are input classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/inputs\n//\n//  TABLE OF CONTENTS\n//  • SHARED STYLES\n//  • WRAPPERS\n//  • INPUT & COPY BLOCKS\n//  • LABELS\n//  • CHECKBOXES\n//    - Modifications\n//    - Validation States\n//  • RADIOS\n//    - Modifications\n//    - Validation States\n//\n//  ============================================================================\n//  $   RADIOS & CHECKBOXES\n//  ----------------------------------------------------------------------------\n.d-checkbox,\n.d-radio {\n    //  Component specific CSS Vars\n    //  ------------------------------------------------------------------------\n    --check-radio--color: var(--primary-color);\n    --check-radio--bc: var(--black-600);\n    --check-radio--bgc: var(--white);\n\n    //  [1] Check to see if we can use custom styles, if so reset the defaults\n    //  ------------------------------------------------------------------------\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        width: 1em;\n        height: 1em;\n\n        //  [2]  Now re-style the checkboxes and radios\n        //  --------------------------------------------------------------------\n        margin: 0;\n        font-size: inherit;\n        background-color: var(--check-radio--bgc);\n        border: var(--su1) solid var(--check-radio--bc);\n        outline: 0;\n        box-shadow: none;\n        cursor: pointer;\n        -webkit-appearance: none;\n           -moz-appearance: none;\n                appearance: none;\n\n        &::-ms-check {\n            display: none;\n        }\n\n        &:focus-visible,\n        &:checked:focus-visible {\n          --check-radio--bc: var(--check-radio--color);\n\n          outline: 0;\n          box-shadow: var(--bs-focus-ring);\n        }\n\n        &:checked {\n            --check-radio--bc: var(--check-radio--color);\n        }\n    }\n\n    flex: 0 auto;\n    align-self: flex-start;\n\n    //  --  DISABLED\n    &[disabled],\n    &--disabled {\n        --check-radio--color: var(--fc-disabled);\n        --check-radio--bc: var(--black-400);\n        --check-radio--bgc: var(--black-300);\n\n        cursor: not-allowed;\n    }\n}\n\n\n//  ============================================================================\n//  $   RADIO / CHECKBOX WRAPPERS\n//  ----------------------------------------------------------------------------\n.d-checkbox-group,\n.d-radio-group {\n    display: flex;\n    padding-right: var(--su1);\n    padding-bottom: var(--su2);\n    padding-left: var(--su1);\n\n    //  --  Wrapper Disabled State\n    &.d-checkbox-group--disabled,\n    &.d-radio-group--disabled {\n        .d-checkbox__label,\n        .d-radio__label {\n            color: var(--fc-disabled);\n            cursor: not-allowed;\n        }\n    }\n}\n\n\n//  ============================================================================\n//  $   INPUT BLOCKS\n//      This holds the radio or checkbox input\n//  ----------------------------------------------------------------------------\n.d-checkbox__input,\n.d-radio__input {\n    display: flex;\n    align-self: flex-start;\n    padding-top: var(--su2);\n}\n\n\n//  ============================================================================\n//  $   COPY BLOCKS\n//      This holds the label and description copy\n//  ----------------------------------------------------------------------------\n.d-checkbox__copy,\n.d-radio__copy {\n    display: inline-flex;\n    flex-direction: column;\n    margin-left: var(--su8);\n}\n\n.d-checkbox__label,\n.d-radio__label {\n    display: inline-flex;\n    flex: 1 auto;\n    flex-direction: column;\n    color: var(--black-800);\n    font-weight: var(--fw-normal);\n    font-size: var(--fs-200);\n    line-height: calc(20/14);\n    cursor: pointer;\n}\n\n\n//  ============================================================================\n//  $   CHECKBOXES\n//  ----------------------------------------------------------------------------\n.d-checkbox {\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        background-repeat: no-repeat;\n        background-position: center center;\n        background-size: contain;\n        border-radius: var(--br2);\n\n        &:focus-visible,\n        &:checked:focus-visible {\n            border-radius: var(--br2);\n        }\n\n        &:checked {\n            --check-radio--bgc: var(--check-radio--color);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='%23fff'/%3E%3C/svg%3E\");\n\n            //  Disabled\n            &[disabled] {\n                --check-radio--bgc: var(--black-300);\n\n                background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n            }\n        }\n\n        &--disabled:checked {\n            --check-radio--bgc: var(--black-300);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n        }\n\n        &--indeterminate,\n        &:indeterminate {\n            --check-radio--bgc: var(--check-radio--color);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='%23fff'/%3E%3C/svg%3E\");\n\n            &[disabled] {\n                --check-radio--bgc: var(--black-300);\n\n                background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n            }\n        }\n\n        &--indeterminate&--disabled,\n        &:indeterminate&--disabled {\n            --check-radio--bgc: var(--black-300);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n        }\n\n        //  Disabled\n        &[disabled],\n        &--disabled {\n            --check-radio--color: var(--fc-disabled);\n            --check-radio--bc: var(--black-400);\n            --check-radio--bgc: var(--black-300);\n        }\n    }\n}\n\n//  $$  CHECKBOX VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-checkbox--warning {\n    --check-radio--color: var(--warning-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n        box-shadow: var(--bs-focus-ring-warning);\n    }\n}\n\n.d-checkbox--error {\n    --check-radio--color: var(--error-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n        box-shadow: var(--bs-focus-ring-error);\n    }\n}\n\n.d-checkbox--success {\n    --check-radio--color: var(--success-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n        box-shadow: var(--bs-focus-ring-success);\n    }\n}\n\n\n//  ============================================================================\n//  $   RADIOS\n//  ----------------------------------------------------------------------------\n.d-radio {\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        border-radius: var(--br-circle);\n\n        // Disabled\n        &[disabled],\n        &--disabled {\n            --check-radio--color: var(--fc-disabled);\n            --check-radio--bc: var(--black-400);\n            --check-radio--bgc: var(--black-300);\n        }\n\n        &:focus-visible,\n        &:checked:focus-visible {\n          box-shadow: var(--bs-focus-ring), inset 0 0 0 var(--su2) var(--white);\n        }\n\n        &:checked {\n            --check-radio--bgc: var(--check-radio--color);\n\n            box-shadow: inset 0 0 0 var(--su2) var(--white);\n\n            // Disabled\n            &[disabled] {\n                --check-radio--bgc: var(--black-400);\n            }\n        }\n\n        &--disabled:checked {\n            --check-radio--bgc: var(--black-400);\n        }\n    }\n}\n\n//  $$  RADIO VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-radio--warning {\n    --check-radio--color: var(--warning-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n      box-shadow: var(--bs-focus-ring-warning), inset 0 0 0 var(--su2) var(--white);\n    }\n}\n\n.d-radio--error {\n    --check-radio--color: var(--error-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n      box-shadow: var(--bs-focus-ring-error), inset 0 0 0 var(--su2) var(--white);\n    }\n}\n\n.d-radio--success {\n    --check-radio--color: var(--success-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n      box-shadow: var(--bs-focus-ring-success), inset 0 0 0 var(--su2) var(--white);\n    }\n}\n","//@import (reference) '../variables/sizes.less';\n//@import (reference) '../variables/icons.less';\n//@import (reference) 'input.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: SELECT MENUS\n//\n//  These are select menu classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/select\n//\n//  TABLE OF CONTENTS\n//  • SELECT MENUS\n//    - Sizes\n//  • SELECT MENU INPUTS\n//    - Validation States\n//\n\n//  ============================================================================\n//  $  SELECT MENU (NATIVE)\n//  ----------------------------------------------------------------------------\n.d-select {\n  //  Component CSS Vars\n  //  --------------------------------------------------------------------------\n  --select--bc: var(--black-500);\n  --select--bgc: var(--white);\n  --select--fc: var(--black-800);\n  --select--bs: var(--focus-ring);\n\n  position: relative;\n  display: inline-flex; // important to the children alignment\n  box-sizing: border-box;\n  width: 100%; // important to fill the available space\n  width: -webkit-fill-available; // important to fill the available space\n  font-weight: inherit;\n  font-size: var(--fs-200);\n  font-family: inherit;\n  line-height: var(--lh-200);\n\n  //  --  Arrows\n  &::before,\n  &::after {\n    position: absolute;\n    right: 0.75em;\n    z-index: var(--zi-selected);\n    border-color: var(--black-900) transparent;\n    border-style: solid;\n    border-width: 0.25em;\n    content: '';\n    pointer-events: none;\n  }\n\n  &::before {\n    top: calc(~'50% - 0.25em');\n    border-top-width: 0;\n  }\n\n  &::after {\n    top: calc(~'50% + 0.125em');\n    border-bottom-width: 0;\n  }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-select--xs {\n  font-size: var(--fs-200);\n\n  .d-select__input {\n    .input-button-xs();\n  }\n}\n\n.d-select--sm {\n  font-size: var(--fs-200);\n\n  .d-select__input {\n    .input-button-sm();\n  }\n}\n\n.d-select--lg {\n  font-size: var(--fs-300);\n\n  .d-select__input {\n    .input-button-lg();\n  }\n}\n\n.d-select--xl {\n  font-size: var(--fs-300);\n\n  .d-select__input {\n    .input-button-xl();\n  }\n}\n\n//  $  SELECT MENU INPUT\n//  ----------------------------------------------------------------------------\n.d-select__input {\n  height: 100%; // Fill the height of its parent\n  padding: 0.7rem 2em 0.7rem 0.8rem !important;\n  color: var(--select--fc);\n  background-color: var(--select--bgc);\n  border-color: var(--select--bc);\n  //  [1] Reset the appearance\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n\n  //  [2] Update the styles\n  .d-input();\n\n  --select--bc: var(--black-600);\n\n  &:focus {\n    --select--bc: var(--primary-color);\n\n    box-shadow: var(--bs-focus-ring);\n  }\n\n  &:disabled {\n    color: var(--fc-disabled);\n    background: var(--black-300);\n    border: 1px solid var(--black-400);\n  }\n\n  &::-moz-focus-inner {\n    outline: none !important;\n  }\n\n  &:-moz-focusring {\n    color: transparent;\n    text-shadow: 0 0 0 var(--black-900);\n  }\n\n  &::-ms-expand {\n    display: none;\n  }\n}\n\n//  $$  VALIDATION STATES\n//  ------------------------------------------------------------------------\n.d-select__input--success {\n  --select--bc: var(--success-color);\n\n  &:focus {\n    --select--bc: var(--success-color);\n\n    box-shadow: var(--bs-focus-ring-success) !important;\n  }\n}\n\n.d-select__input--error {\n  --select--bc: var(--error-color);\n\n  &:focus {\n    --select--bc: var(--error-color);\n\n    box-shadow: var(--bs-focus-ring-error) !important;\n  }\n}\n\n.d-select__input--warning {\n  --select--bc: var(--warning-color);\n\n  &:focus {\n    --select--bc: var(--warning-color);\n\n    box-shadow: var(--bs-focus-ring-warning) !important;\n  }\n}\n\n.d-select--disabled {\n  &::before,\n  &::after {\n    border-color: var(--black-500) transparent;\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: SKELETON\n//\n//  These are the styles for skeleton component.\n//  The --placeholder-from-color and --placeholder-to-color custom properties can be set on the parent class of the\n//  placeholder to control the animation colors.\n//  For further documentation of these styles, please visit https://dialpad.design/components/skeleton\n\n//  ============================================================================\n//  @   SKELETON CONTAINER\n//  ----------------------------------------------------------------------------\n.skeleton-placeholder {\n  display: flex;\n  width: 100%;\n  background: var(--placeholder-from-color, @black-300);\n  animation-duration: 1000ms;\n  fill: var(--placeholder-from-color, @black-300);\n  stroke: none;\n\n  &--animate {\n    animation-name: placeholder-throb;\n    animation-iteration-count: infinite;\n  }\n}\n\n// the animation is used by the skeleton component\n@keyframes placeholder-throb {\n  10% {\n    background: var(--placeholder-from-color, @black-300);\n    fill: var(--placeholder-from-color, @black-300);\n  }\n\n  50% {\n    background: var(--placeholder-to-color, @black-200);\n    fill: var(--placeholder-to-color, @black-200);\n  }\n\n  90% {\n    background: var(--placeholder-from-color, @black-300);\n    fill: var(--placeholder-from-color, @black-300);\n  }\n}\n","//@import (reference) '../utilities/typography.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TABLES\n//\n//  These are table component classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tables\n//\n//  TABLE OF CONTENTS\n//  • DEFAULT STYLE\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-table {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --table--th: var(--black-700);\n    --table--td: var(--black-600);\n    --table--bc: var(--black-400);\n\n    display: table;\n    box-sizing: border-box;\n    width: 100%;\n    max-width: 100%;\n    margin: var(--su0);\n    font-size: var(--fs-200);\n    line-height: var(--lh2);\n    border-collapse: collapse;\n    border-spacing: 0;\n\n    // Caption styles\n    .d-table__caption {\n      margin-bottom: var(--su8);\n      color: var(--table--th);\n      font-weight: var(--fw-bold);\n      font-size: var(--fs-200);\n      .d-ta-unset();\n    }\n\n    // Table Head Styles\n    thead {\n        border-bottom: var(--su2) solid var(--table--bc);\n\n        // Column Header Styles\n        th {\n            font-size: var(--fs-100);\n            text-transform: uppercase;\n        }\n    }\n\n    // Header Styles\n    th {\n        color: var(--table--th);\n        font-weight: var(--fw-bold);\n    }\n\n    // Cell Styles\n    td {\n        color: var(--table--td);\n    }\n\n    // Header & Cell Styles\n    th,\n    td {\n        padding: var(--su16);\n        text-align: left;\n        border-bottom: var(--su1) solid var(--table--bc);\n    }\n\n    // Nested Table Body, First Row Styles, used internally on the Dialtone site\n    tbody + tbody tr:first-of-type {\n        // Header & Cell Styles\n        td,\n        th {\n            border-top: var(--su2) solid var(--table--bc);\n        }\n    }\n\n    // Last Table Row Styles\n    tbody tr:last-of-type {\n        // Header & Cell Styles\n        td,\n        th {\n            border-bottom-width: 0;\n        }\n    }\n}\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-table--inverted {\n    --table--th: var(--black-200);\n    --table--td: var(--black-400);\n    --table--bc: var(--black-200);\n}\n\n//  ============================================================================\n//  $   STRIPED ROW BACKGROUNDS\n//  ----------------------------------------------------------------------------\n.d-table--striped {\n    // Row Styles\n    tr:nth-child(even) {\n        background-color: hsl(var(--black-500-h), var(--black-500-s), var(--black-500-l), 0.1);\n    }\n\n    &.d-table--inverted {\n        // Row Styles\n        tr:nth-child(even) {\n            background-color: hsl(var(--black-200-h), var(--black-200-s), var(--black-200-l), 0.1);\n        }\n    }\n}\n","//@import (reference) '../variables/typography.less';\n//@import (reference) '../variables/spacing.less';\n//@import (reference) '../utilities/internals.less';\n//@import (reference) '../utilities/grid.less';\n\n//\n//  DIALTONE\n//  UTILTIES: TYPOGRAPHY\n//\n//  These are utility typography classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilties/typography\n//\n//  TABLE OF CONTENTS\n//  • FONT-FACE\n//  • FONT FAMILY\n//  • RESET HEADERS & PARAGRAPH MARGINS\n//  • FONT SIZE\n//  • FONT STYLE\n//  • FONT WEIGHT\n//  • LINE HEIGHT\n//  • LISTS\n//  • TEXT ALIGN\n//  • TEXT DECORATION\n//  • TEXT OVERFLOW\n//  • TEXT TRANSFORM\n//  • VERTICAL ALIGN\n//  • WHITESPACE\n//  • WORDBREAK\n//\n//  ============================================================================\n//  $   FONT FACE\n//      To turn on custom webfonts, set @define-font-face to true.\n//  ----------------------------------------------------------------------------\n#d-internals #font-face(@define-ff-custom-adjusted, @define-ff-custom-adjusted-name, normal, true, 95%);\n#d-internals #font-face(@define-ff-custom-adjusted-italic, @define-ff-custom-adjusted-name, italic, true, 95%);\n#d-internals #font-face(@define-ff-mono, @define-ff-mono-name, normal, true);\n#d-internals #font-face(@define-ff-marketing, @define-ff-marketing-name);\n#d-internals #font-face(@define-ff-marketing-semi-expanded, @define-ff-marketing-semi-expanded-name);\n#d-internals #font-face(@define-ff-marketing-expanded, @define-ff-marketing-expanded-name);\n\n@font-face {\n    font-family: 'Segoe UI Adjusted';\n    src: local(Segoe UI Bold);\n    ascent-override: 90%;\n    font-weight: 700;\n}\n//  ============================================================================\n//  $   FONT FAMILY\n//  ----------------------------------------------------------------------------\n.d-ff-custom { font-family: @ff-custom !important; }\n.d-ff-sans { font-family: @ff-sans !important; }\n.d-ff-mono { font-family: @ff-mono !important; }\n.d-ff-marketing { font-family: @ff-marketing !important; }\n.d-ff-marketing-semi-expanded { font-family: @ff-marketing-semi-expanded !important; }\n.d-ff-marketing-expanded { font-family: @ff-marketing-expanded !important; }\n.d-ff-unset { font-family: unset !important; }\n\n//  ============================================================================\n//  $   RESET HEADERS & PARAGRAPH MARGINS\n//  ----------------------------------------------------------------------------\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nol,\nul {\n    margin-top: var(--su0);\n    margin-bottom: var(--su0);\n}\n\n//  ============================================================================\n//  $   BODY STYLES\n//  ----------------------------------------------------------------------------\n.d-body-base          { line-height: var(--lh-400); font-size: var(--fs-200); }\n.d-body-small         { line-height: var(--lh-300); font-size: var(--fs-100); }\n.d-body-compact       { line-height: var(--lh-300); font-size: var(--fs-200); }\n.d-body-compact-small { line-height: var(--lh-200); font-size: var(--fs-100); }\n\n//  ============================================================================\n//  $   LABEL STYLES\n//  ----------------------------------------------------------------------------\n.d-label-base                { line-height: var(--lh-400); font-size: var(--fs-200); font-weight: var(--fw-semibold); }\n.d-label-small               { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-semibold); }\n.d-label-compact             { line-height: var(--lh-300); font-size: var(--fs-200); font-weight: var(--fw-semibold); }\n.d-label-plain               { line-height: var(--lh-400); font-size: var(--fs-200);                                  }\n.d-label-compact-plain       { line-height: var(--lh-300); font-size: var(--fs-200);                                  }\n.d-label-compact-small       { line-height: var(--lh-200); font-size: var(--fs-100); font-weight: var(--fw-semibold); }\n.d-label-plain-small         { line-height: var(--lh-300); font-size: var(--fs-100);                                  }\n.d-label-compact-plain-small { line-height: var(--lh-200); font-size: var(--fs-100);                                  }\n\n//  ============================================================================\n//  $   HELPER STYLES\n//  ----------------------------------------------------------------------------\n.d-helper-base  { line-height: var(--lh-300); font-size: var(--fs-200); }\n.d-helper-small { line-height: var(--lh-200); font-size: var(--fs-100); }\n\n//  ============================================================================\n//  $   CODE STYLES\n//  ----------------------------------------------------------------------------\n\n.d-code-base  { font-family: var(--ff-mono); line-height: var(--lh-200); font-size: var(--fs-200); }\n.d-code-small { font-family: var(--ff-mono); line-height: var(--lh-200); font-size: var(--fs-100); }\n\n//  ============================================================================\n//  $   FONT SIZE\n//  ----------------------------------------------------------------------------\n.d-fs-100 { font-size: var(--fs-100) !important; }\n.d-fs-200 { font-size: var(--fs-200) !important; }\n.d-fs-300 { font-size: var(--fs-300) !important; }\n.d-fs-400 { font-size: var(--fs-400) !important; }\n.d-fs-500 { font-size: var(--fs-500) !important; }\n\n.d-fs-100-mobile { font-size: var(--fs-100-mobile) !important; }\n.d-fs-200-mobile { font-size: var(--fs-200-mobile) !important; }\n.d-fs-300-mobile { font-size: var(--fs-300-mobile) !important; }\n.d-fs-400-mobile { font-size: var(--fs-400-mobile) !important; }\n.d-fs-500-mobile { font-size: var(--fs-500-mobile) !important; }\n\n.d-fs-100-tc8 { font-size: var(--fs-100-tc8) !important; }\n.d-fs-200-tc8 { font-size: var(--fs-200-tc8) !important; }\n.d-fs-300-tc8 { font-size: var(--fs-300-tc8) !important; }\n.d-fs-400-tc8 { font-size: var(--fs-400-tc8) !important; }\n.d-fs-500-tc8 { font-size: var(--fs-500-tc8) !important; }\n\n.d-fs-100-tv { font-size: var(--fs-100-tv) !important; }\n.d-fs-200-tv { font-size: var(--fs-200-tv) !important; }\n.d-fs-300-tv { font-size: var(--fs-300-tv) !important; }\n.d-fs-400-tv { font-size: var(--fs-400-tv) !important; }\n.d-fs-500-tv { font-size: var(--fs-500-tv) !important; }\n\n.d-headline10 { .d-fs-100(); font-weight: @headline-weight; .d-tt-uppercase(); }\n.d-headline24 { .d-fs-300(); font-weight: @headline-weight !important; }\n.d-headline28 { .d-fs-400(); font-weight: @headline-weight !important; }\n.d-headline32 { .d-fs-400(); font-weight: @headline-weight !important; }\n.d-headline36 { .d-fs-400(); font-weight: @headline-weight !important; }\n.d-headline42 { .d-fs-500(); font-weight: @headline-weight !important; }\n.d-headline48 { .d-fs-500(); font-weight: @headline-weight !important; }\n.d-headline54 { .d-fs-500(); font-weight: @headline-weight !important; }\n\n.d-headline-eyebrow            { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-normal); .d-tt-uppercase(); }\n.d-headline-small              { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-bold); }\n.d-headline-soft-small         { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-medium); }\n.d-headline-medium             { line-height: var(--lh-400); font-size: var(--fs-200); font-weight: var(--fw-bold); }\n.d-headline-compact-small      { line-height: var(--lh-200); font-size: var(--fs-100); font-weight: var(--fw-bold); }\n.d-headline-compact-soft-small { line-height: var(--lh-200); font-size: var(--fs-100); font-weight: var(--fw-medium); }\n.d-headline-compact-base,\n.d-headline-compact-medium     { line-height: var(--lh-300); font-size: var(--fs-200); font-weight: var(--fw-bold); }\n.d-headline-compact-large      { line-height: var(--lh-200); font-size: var(--fs-300); font-weight: var(--fw-bold); }\n.d-headline-large              { line-height: var(--lh-400); font-size: var(--fs-300); font-weight: var(--fw-bold); }\n.d-headline-extra-large        { line-height: var(--lh-200); font-size: var(--fs-400); font-weight: var(--fw-medium); }\n.d-headline-extra-extra-large  { line-height: var(--lh-200); font-size: var(--fs-500); font-weight: var(--fw-medium); }\n\n.d-fs-unset { font-size: unset !important; }\n\n//  ============================================================================\n//  $$  FONT STYLE\n//  ----------------------------------------------------------------------------\n.d-fs-normal { font-style: normal !important; }\n.d-fs-italic { font-style: italic !important; }\n.d-fs-unset { font-style: unset !important; }\n\n\n//  ============================================================================\n//  $$  FONT WEIGHT\n//  ----------------------------------------------------------------------------\n.d-fw-normal { font-weight: var(--fw-normal) !important; }\n.d-fw-medium { font-weight: var(--fw-medium) !important; }\n.d-fw-semibold { font-weight: var(--fw-semibold) !important; }\n.d-fw-bold { font-weight: var(--fw-bold) !important; }\n.d-fw-unset { font-weight: unset !important; }\n\n\n//  ============================================================================\n//  $  LINE HEIGHT\n//  ----------------------------------------------------------------------------\n.d-lh0 { line-height: var(--lh0) !important; }\n.d-lh1 { line-height: var(--lh1) !important; }\n.d-lh2 { line-height: var(--lh2) !important; }\n.d-lh4 { line-height: var(--lh4) !important; }\n.d-lh6 { line-height: var(--lh6) !important; }\n.d-lh8 { line-height: var(--lh8) !important; }\n.d-lh12 { line-height: var(--lh12) !important; }\n.d-lh16 { line-height: var(--lh16) !important; }\n.d-lh20 { line-height: var(--lh20) !important; }\n.d-lh24 { line-height: var(--lh24) !important; }\n.d-lh-100 { line-height: var(--lh-100) !important; }\n.d-lh-200 { line-height: var(--lh-200) !important; }\n.d-lh-300 { line-height: var(--lh-300) !important; }\n.d-lh-400 { line-height: var(--lh-400) !important; }\n.d-lh-500 { line-height: var(--lh-500) !important; }\n.d-lh-600 { line-height: var(--lh-600) !important; }\n.d-lh-unset { line-height: unset !important; }\n\n\n//  ============================================================================\n//  $   LISTS\n//  ----------------------------------------------------------------------------\n.d-ls-reset {\n    //  We want to reset everything when applied\n    &,\n    li,\n    ol,\n    ul {\n        margin: @su0;\n        padding: @su0;\n        .d-ls-none();\n    }\n}\n.d-ls-none { list-style: none !important; }\n.d-lst-disc { list-style-type: disc !important; }\n.d-lst-circle { list-style-type: circle !important; }\n.d-lst-content { list-style-type: var(--ls-content) !important; }\n.d-lst-decimal { list-style-type: decimal !important; }\n.d-lst-none { list-style-type: none !important; }\n\n\n//  ============================================================================\n//  $$  TEXT ALIGN\n//  ----------------------------------------------------------------------------\n.d-ta-left { text-align: left !important; }\n.d-ta-right { text-align: right !important; }\n.d-ta-center { text-align: center !important; }\n.d-ta-justify { text-align: justify !important; }\n.d-ta-unset { text-align: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT DECORATION\n//  ----------------------------------------------------------------------------\n.d-td-none { text-decoration: none !important; }\n.d-td-underline { text-decoration: underline !important; }\n.d-td-dotted { text-decoration: underline dotted !important; }\n.d-td-line-through { text-decoration: line-through !important; }\n.d-td-unset { text-decoration: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT OVERFLOW\n//  ----------------------------------------------------------------------------\n.d-to-ellipsis { text-overflow: ellipsis !important; }\n.d-to-clip { text-overflow: clip !important; }\n.d-to-unset { text-overflow: unset !important; }\n\n//  $$  OVERFLOW WRAP\n//  ----------------------------------------------------------------------------\n.d-ow-normal { overflow-wrap: normal !important; }\n.d-ow-anywhere { overflow-wrap: anywhere !important; }\n.d-ow-break-word { overflow-wrap: break-word !important; }\n.d-ow-unset { overflow-wrap: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT TRANSFORM\n//  ----------------------------------------------------------------------------\n.d-tt-uppercase { text-transform: uppercase !important; }\n.d-tt-lowercase { text-transform: lowercase !important; }\n.d-tt-capitalize { text-transform: capitalize !important; }\n.d-tt-full-width { text-transform: full-width !important; }\n.d-tt-none { text-transform: none !important; }\n.d-tt-unset { text-transform: unset !important; }\n\n\n//  ============================================================================\n//  $$  VERTICAL ALIGN\n//  ----------------------------------------------------------------------------\n.d-va-baseline { vertical-align: baseline !important; }\n.d-va-top { vertical-align: top !important; }\n.d-va-bottom { vertical-align: bottom !important; }\n.d-va-text-top { vertical-align: text-top !important; }\n.d-va-text-bottom { vertical-align: text-bottom !important; }\n.d-va-middle { vertical-align: middle !important; }\n.d-va-sub { vertical-align: sub !important; }\n.d-va-super { vertical-align: super !important; }\n.d-va-unset { vertical-align: unset !important; }\n\n\n//  ============================================================================\n//  $$  WHITESPACE\n//  ----------------------------------------------------------------------------\n.d-ws-normal { white-space: normal !important; }\n.d-ws-nowrap { white-space: nowrap !important; }\n.d-ws-pre { white-space: pre !important; }\n.d-ws-pre-line { white-space: pre-line !important; }\n.d-ws-pre-wrap { white-space: pre-wrap !important; }\n.d-ws-break-spaces { white-space: break-spaces !important; }\n.d-ws-unset { white-space: unset !important; }\n\n\n//  ============================================================================\n//  $$  WORD BREAK\n//  ----------------------------------------------------------------------------\n.d-wb-normal { word-break: normal !important; }\n.d-wb-break-all { word-break: break-all !important; }\n.d-wb-keep-all { word-break: keep-all !important; }\n.d-wb-unset { word-break: unset !important; }\n\n\n\n// Unsure what to do with this\n//  $$  TRUNCATE\n//  ----------------------------------------------------------------------------\n.d-truncate {\n    overflow: hidden !important;\n    white-space: nowrap !important;\n    text-overflow: ellipsis !important;\n}\n","//\n//  DIALTONE\n//  COMPONENTS: TABS\n//\n//  These are tab classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tabs\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • ALTERNATE STYLES\n//\n//  ============================================================================\n//  $   WRAPPERS\n//  ----------------------------------------------------------------------------\n.d-tablist {\n    position: relative;\n    display: flex;\n    flex-wrap: wrap;\n    color: var(--fc-tertiary);\n    font-size: var(--fs-200);\n\n    // Add a bottom border unless no border is requested\n    &:not(.d-tablist--no-border)::after {\n        position: absolute;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        z-index: var(--zi-base1);\n        height: var(--su1);\n        background-color: var(--black-400);\n        content: '';\n    }\n}\n\n\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-tab {\n    //  --  COMPONENT CSS VARS\n    //  ------------------------------------------------------------------------\n    --tab--bgc: hsla(var(--white-hsl) ~' / ' 0%);\n    --tab--fc: inherit;\n    --tab--oc: var(--focus-ring);\n\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    margin: 0 var(--su2);\n    padding: var(--su12);\n    color: var(--tab--fc);\n    font: inherit;\n    line-height: 1;\n    background-color: var(--tab--bgc);\n    border: 0;\n    border-radius: var(--br4) var(--br4) 0 0;\n    cursor: pointer;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: var(--td50);\n    transition-property: background-color, color;\n    fill: currentColor;\n\n\n    &:first-of-type {\n        margin-left: 0;\n    }\n\n    &:last-of-type {\n        margin-right: 0;\n    }\n\n    &::after {\n        position: absolute;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        height: var(--su2);\n        background-color: var(--tab--bgc);\n        content: '';\n    }\n\n    &:focus-visible {\n        outline: none;\n        box-shadow: 0 0 0 var(--su2) var(--tab--oc);\n    }\n\n    &:hover {\n        --tab--bgc: var(--black-100);\n        --tab--fc: var(--fc-primary);\n    }\n\n    //  Turn off animations if someone doesn't want them.\n    @media (prefers-reduced-motion) {\n        transition: none;\n    }\n}\n\n\n//  ============================================================================\n//  $   SELECTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tab--selected {\n    --tab--fc: var(--primary-color);\n\n    z-index: var(--zi-selected);\n\n    &:hover {\n        --tab--bgc: hsla(var(--primary-color-hsl) ~' / ' 10%);\n        --tab--fc: var(--primary-color-hover);\n    }\n\n    &::after,\n    &:hover::after {\n        --tab--bgc: var(--primary-color);\n    }\n}\n\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tablist--inverted {\n    --tab--fc: var(--white);\n\n    &::after {\n        background-color: hsla(var(--purple-600-hsl) ~' / ' 50%);\n    }\n\n    .d-tab {\n        --tab--fc: var(--white);\n\n        &:hover {\n            --tab--bgc: hsla(var(--white-hsl) ~' / ' 10%);\n        }\n    }\n\n    .d-tab--selected {\n        --tab--fc: var(--white);\n        --tab--oc: var(--white);\n\n        &::after {\n            --tab--bgc: currentColor;\n        }\n    }\n}\n\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n.d-tablist--sm {\n    font-size: var(--fs-100);\n\n    .d-tab {\n        padding: var(--su12);\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: TOOLTIPS\n//\n//  These are tooltip classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tooltips\n//\n//  TABLE OF CONTENTS\n//  • VARIABLES\n//  • BASE STYLE\n//  • ALTERNATE STYLES\n//  • ARROW DIRECTIONS\n//    - TOP LEFT\n//    - TOP CENTER\n//    - TOP RIGHT\n//    - RIGHT TOP\n//    - RIGHT CENTER\n//    - RIGHT BOTTOM\n//    - BOTTOM LEFT\n//    - BOTTOM CENTER\n//    - BOTTOM RIGHT\n//    - LEFT TOP\n//    - LEFT CENTER\n//    - LEFT BOTTOM\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip-container {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.d-tooltip {\n    //  --  COMPONENT CSS VARS\n    //  -----------------------\n    --tooltip--bgc: var(--black-800);\n    --tooltip--fc: var(--white);\n\n    z-index: var(--zi-tooltip);\n    max-width: var(--size216);\n    padding: var(--su8);\n    color: var(--tooltip--fc);\n    font-size: var(--fs-100);\n    line-height: var(--lh-300);\n    text-align: center;\n    background-color: var(--tooltip--bgc);\n    border-radius: var(--br4);\n\n    &::after {\n        position: absolute;\n        border: var(--su6) solid transparent;\n        content: '';\n    }\n}\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--inverted {\n    //  --  COMPONENT CSS VARS\n    //  -----------------------\n    --tooltip--bgc: var(--black-200);\n    --tooltip--fc: var(--black-700);\n}\n\n//  ============================================================================\n//  $   HIDE STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--hide {\n    visibility: hidden;\n    opacity: 0;\n    transition:\n        visibility 0s 200ms,\n        transform 200ms var(--ttf-in-out) 0s,\n        opacity 200ms var(--ttf-in-out) 0s;\n}\n\n//  ============================================================================\n//  $   SHOW STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--show {\n    transform: translate(0, 0);\n    visibility: visible;\n    opacity: 1;\n    transition:\n        visibility 0s 0s,\n        transform 200ms var(--ttf-in-out) 10ms,\n        opacity 200ms var(--ttf-in-out) 10ms;\n}\n\n//  ============================================================================\n//  $   HOVER AND FOCUS STYLES\n//  ----------------------------------------------------------------------------\n.d-tooltip--hover {\n    .d-tooltip {\n        &:extend(.d-tooltip--hide);\n    }\n\n    &:hover,\n    &:focus-visible {\n        .d-tooltip {\n            &:extend(.d-tooltip--show);\n        }\n    }\n}\n\n//  ============================================================================\n//  $   TOOLTIP DIRECTIONS\n//  ============================================================================\n//  $$  TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--top-right {\n  top: calc(100% + var(--su12));\n  transform: translateY(var(--su16));\n\n  &::after {\n    top: var(--sun6);\n    border-top-width: 0;\n    border-bottom-color: var(--tooltip--bgc);\n  }\n}\n\n//  $$  BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--bottom-left,\n.d-tooltip__arrow--bottom-center,\n.d-tooltip__arrow--bottom-right {\n  bottom: calc(100% + var(--su12));\n  transform: translateY(var(--sun16));\n\n  &::after {\n    bottom: var(--sun6);\n    border-top-color: var(--tooltip--bgc);\n    border-bottom-width: 0;\n  }\n}\n\n//  $$  TOP / BOTTOM LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--bottom-left {\n  left: var(--sun2);\n\n  &::after {\n    left: var(--su16);\n  }\n}\n\n//  $$  TOP / BOTTOM CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--bottom-center {\n  &::after {\n    left: 50%;\n    margin-left: var(--sun6);\n  }\n}\n\n//  $$  TOP / BOTTOM RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--top-right,\n.d-tooltip__arrow--bottom-right {\n  right: var(--sun2);\n\n  &::after {\n    right: var(--su16);\n  }\n}\n\n//  $$  RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--right-bottom {\n  right: calc(100% + var(--su12));\n  transform: translateX(var(--sun16));\n\n  &::after {\n    right: var(--sun6);\n    border-right-width: 0;\n    border-left-color: var(--tooltip--bgc);\n  }\n}\n\n//  $$  LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow--left-top,\n.d-tooltip__arrow--left-center,\n.d-tooltip__arrow--left-bottom {\n  left: calc(100% + var(--su12));\n  transform: translateX(var(--su16));\n\n  &::after {\n    left: var(--sun6);\n    border-right-color: var(--tooltip--bgc);\n    border-left-width: 0;\n  }\n}\n\n//  $$  RIGHT / LEFT TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--left-top {\n  top: var(--sun1);\n\n  &::after {\n    top: var(--su8);\n  }\n}\n\n//  $$  RIGHT / LEFT CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--left-center {\n  &::after {\n    top: 50%;\n    margin-top: var(--sun6);\n  }\n}\n\n//  $$  RIGHT / LEFT BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-bottom,\n.d-tooltip__arrow--left-bottom {\n  bottom: var(--sun1);\n\n  &::after {\n    bottom: var(--su8);\n  }\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TOGGLE\n//\n//  These are toggle classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/toggle\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • TOGGLE VARIANTS\n//  • CHECKED TOGGLE\n//  • INDETERMINATE TOGGLE\n//  • FOCUSED TOGGLE\n//  • DISABLED TOGGLE\n//\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-toggle {\n  //  Component specific CSS Vars\n  //  ------------------------------------------------------------------------\n  --toggle-ta: var(--ttf-in-out);\n  --toggle-ts: var(--td200);\n  --toggle-bgc: var(--black-400);\n  --toggle-height: var(--su24);\n  --toggle-width: var(--su48);\n  --toggle-icon-size: var(--su16);\n\n  //  $$  BASE STYLE\n  //  ----------------------------------------------------------------------------\n  position: relative;\n  display: inline-block;\n  box-sizing: border-box;\n  width: var(--toggle-width);\n  height: var(--toggle-height);\n  padding: 0;\n  line-height: var(--lh4);\n  vertical-align: middle;\n  background-color: var(--toggle-bgc);\n  border: var(--su1) solid var(--toggle-bgc);\n  border-radius: var(--toggle-width);\n  cursor: pointer;\n\n  &__inner {\n    position: absolute;\n    top: calc(var(--su4) - var(--su1));\n    left: var(--su24);\n    width: var(--toggle-icon-size);\n    height: var(--toggle-icon-size);\n\n    .d-toggle--small & {\n      top: var(--su1);\n      left: calc(var(--su16) + var(--su1));\n    }\n\n    .d-toggle--indeterminate & {\n      display: none;\n    }\n\n    color: var(--white);\n    background-image: url('data:image/svg+xml;base64,');\n    background-repeat: no-repeat;\n    background-size: cover;\n  }\n\n  &::after {\n    position: absolute;\n    top: var(--su1);\n    left: var(--su1);\n    width: calc(var(--toggle-icon-size) + var(--su4));\n    height: calc(var(--toggle-icon-size) + var(--su4));\n    background-color: var(--white);\n    border-radius: var(--br-circle);\n    cursor: pointer;\n    transition: left var(--toggle-ts) var(--toggle-ta);\n    content: ' ';\n  }\n\n  //  $$  TOGGLE VARIANTS\n  //  ----------------------------------------------------------------------------\n  &--small {\n    --toggle-height: calc(var(--su16) + var(--su2));\n    --toggle-width: calc(var(--su32) + var(--su4));\n    --toggle-icon-size: calc(var(--su12) + var(--su2));\n\n    &::before {\n      position: absolute;\n      top: calc(var(--su4) * -1);\n      right: 0;\n      bottom: calc(var(--su4) * -1);\n      left: 0;\n      content: '';\n    }\n  }\n\n  &--small::after {\n    width: var(--toggle-icon-size);\n    height: var(--toggle-icon-size);\n  }\n\n  //  $$  CHECKED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--checked {\n    --toggle-bgc: var(--primary-color);\n\n    .d-toggle__inner {\n      left: var(--su6);\n      background-image: url('data:image/svg+xml;base64,');\n    }\n\n    &.d-toggle--small .d-toggle__inner {\n      left: calc(var(--su2) + var(--su1));\n    }\n\n    &::after {\n      left: calc(var(--su24) + var(--su1));\n    }\n\n    &.d-toggle--small::after {\n      right: var(--su1);\n      left: auto;\n    }\n  }\n\n  //  $$  INDETERMINATE TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--indeterminate {\n    &__inner {\n      display: none;\n    }\n\n    &::after {\n      left: 50%;\n      transform: translate(-50%, 0);\n    }\n  }\n\n  //  $$  FOCUSED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &:focus-visible {\n    outline: none;\n    box-shadow: var(--bs-focus-ring);\n  }\n\n  //  $$  DISABLED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--disabled,\n  &[disabled] {\n    cursor: not-allowed;\n    opacity: 0.5;\n\n    &::after {\n      cursor: not-allowed;\n      transition-property: none;\n    }\